  getInvoiceNumber(num: number): string {
    let displayed = num.toString();
    while (displayed.length <= 8) {
      displayed = '0' + displayed;
    }
    return displayed;
  }
  getNumComp(): number {
    let numero = 1;
    let unico = false;
    console.log(this.numeros);
    while (unico == false) {
      let count = 0;
      for (let i = 0; i < this.numeros.length; i++) {
        const num = this.numeros[i];
        if (num.numero == numero) count++;
      }
      if (count >= 1) {
        numero++;
      } else {
        unico = true;
      }
    }
    return numero;
  }
  onPay() {
    if (!this.pagoActual || this.pagoActual == '' || this.pagoActual == null || this.pagoActual == 'debe') {
      this.launchAlert('error', 'Error', 'Seleccionar un metodo de pago', null, false, 3500, false, 'center', 'row', true);
    } else {
      this.getNumeros();
      const hoy = new Date();
      const year = hoy.getFullYear().toString();
      let month = (hoy.getMonth() + 1).toString();
      if (month.length <= 1) {
        month = '0' + month;
      }
      this.initDatoFactura();
      let day = hoy.getDate().toString();
      if (day.length <= 1) {
        day = '0' + day;
      }
      console.log(this.numeros);

      const fecha = day + month + year;
      if (this.numeros) {
        this.factura.numero = this.getNumComp();
        this.numComp = this.getInvoiceNumber(this.factura.numero);
        console.log('factura?: ', this.numComp);
      } else {
        console.log('sin numeros');
        this.factura.numero = 1;
        this.numComp = '000000001';
      }

      this.factura.fecha = hoy.toString();
      this.factura.pago = this.pagoActual;
      this.factura.numero = parseInt(this.numComp);
      this.claveAccesso = fecha + this.datoActual.tipoComp + this.datoActual.ruc
      this.claveAccesso = this.claveAccesso + this.datoActual.tipoAmb + this.datoActual.serie
      this.claveAccesso = this.claveAccesso + this.numComp + this.datoActual.codNum + this.datoActual.tipEmi;
      // this.pruebaMod();
      this.claveAccesso += this.codigoVerificador(this.claveAccesso);
      console.log(this.claveAccesso);
      this.factura.claveAccesso = this.claveAccesso;
      this.factura.datoFacturado = this.datoActual._id;
      this._billService.pay(this.factura).subscribe(
        response => {
          if (response.registro && response.factura) {
            console.log('cobrado correctamente');
            this.factura = response.factura;
            this.register = response.registro;
            this.register.cancelado = true;
            this.register.facturado = true;
            localStorage.removeItem('register');
            localStorage.setItem('invoice', this.register._id);
          }
          this.getFactura();
          this.launchAlert('success', 'Factura', response.Message, false, false, 3500, null, 'top', 'row', true)
        }
      )
    }
  }
