// tslint:disable: variable-name
import { Component, OnInit } from '@angular/core';
import { RateService } from '../../../../../service/rate.service';
import { Location } from '@angular/common';
import { LimitService } from '../../../../../service/limit.service';

@Component({
  selector: 'app-list-rate',
  templateUrl: './list-rate.component.html',
  styleUrls: ['./list-rate.component.css'],
  providers: [RateService, LimitService]
})
export class ListRateComponent implements OnInit {
  public title: string;
  public rates: any[];
  constructor(
    private _rateService: RateService,
    private location: Location,
    private _limitService: LimitService
  ) {
    this.title = 'cargando';
    this.rates = [];
    this.initRates();
  }
  initRates() {
    let tempRates;
    this._rateService.getRate().subscribe(
      response => {
        if (response.Rates && (response.Rates.length >= 1)) {
          tempRates = response.Rates;
          for (const rate of tempRates) {
            this._limitService.getLimits(rate._id).subscribe(
              response => {
                rate.limits = response.Limits;
                rate.limits.sort((menor, mayor) => (menor.limit_from - mayor.limit_from));
                console.log(rate);
              }, error => {
                console.log(error as any);
              }
            );
          }
          this.rates = tempRates;
          this.title = 'Lista de Tarifas Registradas:';
        } else {
          this.title = 'Lista de Tarifas Registradas: Aun no hay tarifas';
        }
      }, error => {
        console.log(error as any);
      }
    );
  }
  ngOnInit() {
  }

  return() {
    this.location.back();
  }
}
