import { Component, OnInit } from '@angular/core';
import { NumerosService } from '../../../../service/numeros.service';
import { Numeros } from '../../../../models/numeros';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-numeros',
  templateUrl: './numeros.component.html',
  styleUrls: ['./numeros.component.css'],
  providers: [NumerosService]
})
export class NumerosComponent implements OnInit {
  public title: string;
  public numeros: Numeros;
  public cargando: boolean;
  public existingNumeros;
  public rango_min: boolean;
  public rango_max: boolean;
  public fechaValida: boolean;
  public anterior: boolean;
  public mostrarNumeros: boolean;
  constructor(
    private _numerosService: NumerosService
  ) {
    this.title = 'Cargando';
    this.cargando = true;
    this.initNumeros();
    this.mostrarNumeros = false;
  }
  initNumeros() {
    this.numeros = {
      num_min: null,
      num_max: null,
      caduca: (new Date).toString(),
      estado: true,
      codigoTalonario: '',
      updated_at: '',
      created_at: '',
      updated_by: '',
      created_by: ''
    };
    this.title = 'Rango de facturas';
    this.cargando = false;
    this.mostrarNumeros = true;
    this.initExsistNumeros();
  }
  initExsistNumeros() {
    this._numerosService.showRaw().subscribe(
      response => {
        console.log(response);
        if (!response.Numeros) {
          this.launchAlert('warning', 'Aun no existen rangos de facturas', null, null, null, 3500, null, 'top', 'row', true);
        } else {
          this.existingNumeros = response.Numeros;
          this.existingNumeros.sort((a,b) => (b.num_min - a.num_min));
          const siguiente = this.existingNumeros[0].num_max + 1;
          this.numeros.num_min = siguiente;
          this.numeros.num_max = siguiente + 99;
          this.anterior = true;
          console.log(this.numeros);
          
        }
      }
    );
  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
  comprobarFecha(fecha) {
    console.log(fecha);
    var ingresando = new Date(fecha);
    if (this.existingNumeros && this.existingNumeros.length >= 1) {
      for (const rangos of this.existingNumeros) {
        console.log(rangos.caduca);
        var ingresado = new Date(rangos.caduca);
        if (ingresado >= ingresando) {
          console.log('error fecha repetida');
          this.launchAlert('error', 'La fecha seleccionada es Invalida', null, null, null, 3500, null, 'top', 'row', true);
          this.fechaValida = false;
        } else {
          this.launchAlert('success', 'Fecha seleccionada Valida', null, null, null, 3500, null, 'top', 'row', true);
          this.fechaValida = true;
        }
      }
    } else {
      this.fechaValida = true;
    }
  }
comprobarCodigo(codigo) {
  var count = 0;
  if (codigo && codigo.length <= 5) count ++;
  if (this.existingNumeros && this.existingNumeros.length >= 1) {
    for (const rangos of this.existingNumeros) {
      if (rangos.codigoTalonario == codigo) count ++;
    }
  }
  if (count >=1) {
    this.launchAlert('error', 'Cdigo Incorrecto', null, null, null, 3500, null, 'top', 'row', true);
  }
  if (count = 0) {
    this.launchAlert('success', 'Cdigo Correcto', null, null, null, 3500, null, 'top', 'row', true);

  }
}
  comprobarNumeros(rango, origen) {
    let count = 0;
    if (this.existingNumeros && this.existingNumeros.length >= 1) {
      for (const rangos of this.existingNumeros) {
        if (rangos.num_min >= rango || rangos.num_max >= rango) {
        count++;
        }
      }
    }
    if (this.numeros.num_max && this.numeros.num_min) {
      if (this.numeros.num_min >= this.numeros.num_max) {
        count++;
      }
    }
    if (count == 0) {
      if (origen == 'min') {
        this.launchAlert('success', 'Rango mnimo correcto',
        null, null, null, 3500, null, 'top', 'row', true);
        this.rango_min = true;
      } else
      if (origen == 'max') {
        this.launchAlert('success', 'Rango mximo correcto',
        null, null, null, 3500, null, 'top', 'row', true);
        this.rango_max = true;
      }
      
    } else if (count >= 1) {
      this.launchAlert('error', 'Error, el Nuevo rango no puede ser menor o igual a los ultimos rangos',
      null, null, null, 3500, null, 'top', 'row', true);
      if (origen == 'min') {
        this.rango_min = false;
      } else
      if (origen == 'max') {
        this.rango_max = false;
      }
    }
  }
  onSubmit() {
    console.log(this.numeros);
    if (this.rango_max == true && this.rango_min == true) {
      this.onSave();
    } else {
      this.launchAlert('error', 'Error, verificar los rangos',null,null,null,3500, null,'top','row', true);
    }
  }
  onSave() {
    this._numerosService.save(this.numeros).subscribe(
      response => {
        this.initExsistNumeros();
        this.launchAlert('success', response.Message, null, null, null, 3500, null, 'top', 'row', true);
        this.mostrarNumeros = false;
        this.mostrarNumeros = true;
        console.log(response);
      }
    );
  }
  ngOnInit() {

  }
  changeView() {
    if (this.mostrarNumeros == true) this.mostrarNumeros = false; else
    if (this.mostrarNumeros == false) this.mostrarNumeros = true;
  }
}
