import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { UserService } from '../../../../service/user.service';

@Component({
  selector: 'app-contact-component',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})

export class ContactComponent implements OnInit {
  public title;
  public employes: any[];
  constructor(
    private location: Location,
    private userService: UserService
  ) {
    window.document.title = 'MerAgua - Contctanos';
    this.employes = [];
    this.title = 'Departamento de Agua Potable del GAD de Mera';
  }

  ngOnInit() {
    this.userService.showRaw().subscribe(
      response => {
        for (const usuario of response.Usuarios) {
          if (usuario.role_user.toLowerCase() !== 'common' && usuario.role_user.toLowerCase() !== 'simple') {
            this.employes.push(usuario);
          }
        }
        console.log(this.employes);
      }, error => {
        console.error(error as any);
      }
    )
  }

  return() {
    this.location.back();
  }
}
