  separarArrays() {
    console.log(this.month + '-' + this.year);
    this.fecha = this.month + '/01/' + this.year;
    this.array1 = [];
    this.anterior = null;
    for (let i = 0; i < this.arrayT.length; i++) {
      const element = this.arrayT[i];
      if (element.year == this.year) {
        if (element.month < this.month) {
          this.array1.push(element);
          console.log(this.month - 1);
          if ((this.month - 1) > 0) {
            this.anterior = (this.month - 1) + '/' + this.year;
          }
        }
      } else if (element.year < this.year) {
        this.array1.push(element);
        if (this.month - 1 == 0) {
          this.anterior = (12) + '/' + (this.year - 1);
        }
      }
    }
    this.comprobarArray()
  }

  comprobarArray() {
    for (let index = 0; index < this.arrayT.length; index++) {
      const element = this.arrayT[index];
      let fecha = this.anterior.split('/');
      let year = fecha[1];
      let month = fecha[0];
      if (element.year == year && element.month == month) {
        console.log('existe');

      }
    }
  }
