  public numeros;
  initNumeros() {
    this.numerosService.showRaw().subscribe(
      response => {
        this.numeros = response.Numeros;
        this.aumentarNumero();
      }
    );
  }
  public numero: number = 0;
  aumentarNumero() {
    this.numeros.sort((a, b) => (b.num_min - a.num_min));
    let numerosValidos = [];
    for (const numero of this.numeros) {
      let hoy = new Date;
      let fecha = new Date(numero.caduca);
      if (hoy < fecha) {
        numerosValidos.push(numero);
      }
    }
    console.log(numerosValidos);
    this.statisticsService.getBillsNumbers().subscribe(
      response => {
        console.log(response);
        this.numero = numerosValidos[0].num_min;
        if (response.numeros) {
          let nuevo = false;
          while (nuevo == false) {
            for (const numero of response.numeros) {
              if (this.numero == numero.numero) {
                nuevo = false;
                this.numero++;
              } else {
                nuevo = true;
              }
            }
          }
        }
      }
    )

  }
public cadena;
  cargarCadena() {
    this.cadena = this.aleatorio();
  }

  aleatorio(): string {
    let chars = '0123456789abcdef';
    let size = 10;
    let cadena = '';
    for (let i = 0; i < 10; i++) {
      cadena += chars[Math.floor(Math.random() * (chars.length))];
    }
    return cadena;
  }
