import { Component, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { User } from './models/user';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  public title = 'client';
  public smallText: boolean;
  public identity: User;
  constructor(
    private router: Router,
    private _location: NgZone
  ) {
    this.smallText = false;
    if ( !localStorage.getItem('identity') || localStorage.getItem('identity') == null) {
      this.router.navigate(['/consultas']);
    } else {
      this.identity = JSON.parse(localStorage.getItem('identity'));
    }
  }
  changeRol() {
    const tempIdentity = JSON.parse(localStorage.getItem('identity'));
    if (tempIdentity.role_user != this.identity.role_user) {
      localStorage.setItem('identity', JSON.stringify(this.identity));
      this._location.runOutsideAngular(()=>{
        location.reload();
      });
    }
  }
}
