// tslint:disable: curly
// tslint:disable: triple-equals
import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { RateService } from '../../../../../service/rate.service';
import { Rate } from '../../../../../models/rate';
import { LimitService } from '../../../../../service/limit.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';


@Component({
  selector: 'app-add-rate',
  templateUrl: './addrate.component.html',
  styleUrls: ['./addrate.component.scss'],
  providers: [RateService, LimitService]
})

export class AddRateComponent implements OnInit, OnChanges {
  public title: string;
  public rate: Rate;
  public rateId: string;
  public limits;
  public nextLimit;
  public limitAnterior: boolean;
  public noMoreLimits: boolean;
  public limitsEditeds: boolean;
  public newLimits: boolean;
  public isEditing: boolean;
  public identity;
  public listar: boolean;
  public mostrando: boolean;
  @Input() rateEdit;
  @Output() devolverCambios = new EventEmitter();

  constructor(
    private location: Location,
    private rateService: RateService,
    private limitService: LimitService,
    private router: Router
  ) {
    this.identity = JSON.parse(localStorage.getItem('identity'));
    window.document.title = 'MerAgua - Agregar Tarifa';
    this.listar = true;
    this.mostrando = true;
    if (this.identity.role_user.toLowerCase() == 'operador') this.router.navigate(['/inicio']);
    this.title = 'Control de Tarifas';
    this.initRate();
    this.limits = [] = [];
    this.nextLimit = {
      limit_from: 0,
      limit_to: 1,
      cost: 0,
      percent_cost: false
    };
    this.limitsEditeds = null;
  }
  return() {
    this.location.back();
  }
  keyPressOnForm(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
    }
  }
  recargarVista() {
    this.mostrando = false;
    setTimeout(() => {
      this.mostrando = true;
    }, 250);
  }
  limpiar() {
    this.limits = [] = [];
    this.noMoreLimits = null;
    setTimeout(() => {
      this.limitAnterior = null;
      this.nextLimit.limit_to = 1;
      this.nextLimit.limit_from = 0;
      this.nextLimit.cost = 0;
    }, 1);
    console.log(this.nextLimit);
    this.recargarVista();
  }
  initRate() {
    if (!this.rateEdit) {
      this.rate = new Rate(
        '',
        '',
        0,
        false,
      );
    } else if (this.rateEdit) {
      this.rate = this.rateEdit;
      console.log('edicion');
      this.isEditing = true;

    }
  }
  ngOnInit() {
    this.initRate();
  }
  ngOnChanges() {
    if (this.rateEdit) {
      this.rate = this.rateEdit;
      this.isEditing = true;
      console.log('edicion');
      this.limitService.getLimits(this.rate._id).subscribe(
        response => {
          console.log(response);
          response.Limits.sort((a, b) => (a.limit_from - b.limit_from));
          if (response.Limits.length <= 0) {
            this.limitsEditeds = true;
            this.newLimits = true;
          } else {
            this.limits = response.Limits;

            this.noMoreLimits = true;
            this.nextLimit.limit_to = null;
            this.nextLimit.limit_from = null;
            this.nextLimit.cost = null;
            this.nextLimit.percent_cost = this.limits[0].percent_cost;
          }
        }
      );
    }
  }
  removeLimits(index) {
    this.isEditing = null;
    if (this.rateEdit) {
      this.limitsEditeds = true;
    }
    if (this.limits[index].limit_to >= 999999 || index === this.limits.length) {
      this.noMoreLimits = null;
      this.limitAnterior = true;
    }
    if (index + 1 < this.limits.length) {
      if (index - 1 >= 0) {
        this.limits[index + 1].limit_from = (this.limits[index - 1].limit_to + 1);
      } else if (index - 1 < 0) {
        this.limits[index + 1].limit_from = 0;
      }
      this.limits.splice(index, 1);

    } else if (index + 1 === this.limits.length && this.limits.length >= 1) {
      if (index - 1 < 0) {
        this.nextLimit.limit_from = 0;
        this.nextLimit.limit_to = 1;
      } else {
        this.nextLimit.limit_from = this.limits[index - 1].limit_to + 1;
        this.nextLimit.cost = this.limits[index - 1].cost + 0.05;
        this.nextLimit.limit_to = this.nextLimit.limit_from + 1;
      }
      this.limits.splice(index, 1);
    }

    if (this.limits.length && this.limits.length >=1) {
      this.limits[0].cost = 0;
    }

    if (this.limits.length <= 0) {
      this.limitAnterior = null;
      this.noMoreLimits = null;
      this.nextLimit.limit_to = 1;
      this.nextLimit.limit_from = 0;
      this.nextLimit.cost = 0;
      this.limits = [] = [];
    }
  }
  addLimits() {
    if (!this.noMoreLimits) {
      if (this.nextLimit.percent_cost === 'true') {
        this.nextLimit.percent_cost = true;
      } else if (this.nextLimit.percent_cost === 'false') {
        this.nextLimit.percent_cost = false;
      }
      if (this.limitAnterior === true && (this.limits[this.limits.length - 1].cost >= this.nextLimit.cost)) {
        alert('error, el costo del siguiente limite no puede ser menor que el anterior');
      } else if (this.nextLimit.limit_from < 0 ||
        (this.nextLimit.limit_to <= 0) ||
        this.nextLimit < 0 ||
        this.nextLimit.limit_to <= this.nextLimit.limit_from) {
        alert('error, limites negativos');
      } else if (this.nextLimit.limit_to === -1) {
        this.nextLimit.limit_to = 999999;
        this.noMoreLimits = true;
      } else {
        this.limitAnterior = true;
        this.limits.push(this.nextLimit);
        this.nextLimit = {
          limit_from: this.nextLimit.limit_to + 1,
          limit_to: this.nextLimit.limit_to + 2,
          cost: this.nextLimit.cost + 0.05,
          percent_cost: this.nextLimit.percent_cost
        };
      }
    } else {
      alert('Para volver a Ingresar Limites dar Click en Cancelar|Limpiar');
    }
  }

  finishLimits() {
    if (!this.noMoreLimits) {
      this.nextLimit.limit_to = 999999999;
      this.limits.push(this.nextLimit);
      this.noMoreLimits = true;
    } else if (this.noMoreLimits === true) {
      alert('Para volver a Ingresar Limites dar Click en [Cancelar|Limpiar] o eliminar el Ultimo Limite');
    }

  }
  onSubmit(form) {
    if (!this.rateEdit) {
      this.rateService.save(this.rate).subscribe(
        response => {
          // devuelve response.Tarifa
          alert(response.Message);
          const id = response.Tarifa._id;
          this.saveLimits(id);
          form.reset();
          this.limits = [], [];
        }, error => {
          console.log(error as any);
        }
      );
    } else if (this.rateEdit) {
      console.log('rate edit ', this.rateEdit);
      console.log('rate', this.rate);
      this.rate._id = this.rateEdit._id;
      this.rateService.update(this.rate).subscribe(
        response => {
          console.log('respuesta del servidor', response);
          const id = response.Tarifa._id;
          this.dropOldLimits(response.Tarifa._id);
          // if (this.limitsEditeds && !this.newLimits) {
          //   this.limitService.deleteLimits(this.rate._id).subscribe(
          //     responseLimits => {
          //       console.log('limites borrados');
          //       if (responseLimits) {
          //         this.saveLimits(this.rate._id);
          //       }
          //     }, errorLimits => {
          //       console.log(errorLimits as any);
          //     });
          // } else if ( this.newLimits && this.limits.length >= 1) {
          //   console.log('ingresando nuevos limites');
          //   this.saveLimits(this.rate._id);
          // }
        }, error => {

        }
      );
    }
    setTimeout(() => {
      this.devolverCambios.emit();
    }, 500);
  }

  editarLimites() {
    alert('si edita los limites se borraran los anteriores y se crearan nuevamente');
  }

  dropOldLimits(id) {
    console.log('borrando limites');

    this.limitService.deleteLimits(id).subscribe(
      response => {
        console.log(response);
        this.saveLimits(id);
      }, error => {
        console.log(error as any);
      }
    )
  }

  saveLimits(id) {
    console.log('guardando limites');
    for (let index = 0; index < this.limits.length; index++) {
      const limit = this.limits[index];
      limit.cost = parseFloat(limit.cost.toFixed(2));
      console.log(limit);
      this.limitService.save(limit, id).subscribe(
        responseLimitsedited => {
          console.log(responseLimitsedited.Message);
        }, error => {
          console.log(error as any);
        }
      );
    }
    this.recargarVista();
  }

}
