import { Component, OnInit, DoCheck, Directive } from '@angular/core';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { Global } from '../../../../../service/global';
import { UsersComponent } from '../users/users.component';
import { Location } from '@angular/common';
import { StatisticsService } from '../../../../../service/statistics.service';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register-simple',
  templateUrl: './register-simple.component.html',
  styleUrls: ['./register-simple.component.scss'],
  providers: [UserService, UsersComponent, StatisticsService]

})
export class RegisterSimpleComponent {
  public title: string;
  public identity;
  public url: string;
  public stats;
  public user: User;
  public token;
  public status: boolean;
  public recarga;
  public onEdit: boolean;
  public cedulaWarning: boolean;
  public cedulaCorrecta: boolean;
  public cedulaUnica: boolean;
  public cedulas: any[];
  public nombreCorrecto: boolean;
  public apellidoCorrecto: boolean;
  public direccionCorrecta: boolean;
  constructor(
    private userService: UserService,
    private location: Location,
    private statisticsService: StatisticsService,
    private router: Router
  ) {
    this.title = 'Registro de Usuarios Comunes';
    window.document.title = 'MerAgua - Ingreso de Usuarios';
    this.identity = this.userService.getIdentity();
    if (this.identity.role_user.toLowerCase() != 'admin' && this.identity.role_user.toLowerCase() != 'secretario') this.router.navigate(['/inicio']);
    this.stats = this.userService.getStats();
    this.token = userService.getToken();
    this.url = Global.url + 'user/';
    this.cedulaWarning = false;
    this.cedulaCorrecta = false;
    this.initCedulas();
    this.initUsers();
    this.nombreCorrecto = true;
  }
  // metodo para recolectar el usuario a editar en caso de que se haya enviado uno
  editarUser(user): void {
    user = JSON.parse(user);
    this.user = user;
    this.onEdit = true;
    this.cedulaWarning = true;
  }
  // metodo a manera de Constructor para el objeto "user"
  initUsers() {
    this.user = new User(
      '',           // "_id"
      null,         // "cuenta"
      null,         // "contrase"
      null,         // "correo"
      '',           // "nombre"
      '',           // "image"
      '',           // "apellido"
      '',           // "cedula"
      '',           // "direccion"
      '',           // "telefono"
      '',           // "fecha_nacimiento"
      '',           // "sexo"
      'Simple',     // "role_user"
      true,         // "activa"
      ''
    );
  }
  // metodo para navegar hacia atras
  return() {
    this.location.back();
  }
  // metodo OnSubmit (al enviar)
  onSubmit(form) {
    // funcion de deteccion si el objeto enviado es ingreso o edicion
    if (!this.onEdit) {
      // si es ingreso de un nuevo usuario se llama al metodo Save
      this.save(form);
    } else if (this.onEdit) {
      // si se esta editando un usuario se llama al metodo Update
      console.log('preparando edicion');
      this.update(form);
    }
  }
  // Metodo de envio de datos hacia el backend
  save(form) {
    // llamado al servicio del usuario para enviar el nuevo objeto [user] a la base de datos
    this.userService.registerSimple(this.user).subscribe(
      response => {
        // en caso de que el backend responda con los datos del usuario editado se presenta un mensaje de correcto
        if (response.user && response.user._id) {
          this.status = true;
          form.reset();
          this.launchAlert(
            'success',
            'Se ha Ingresado un Nuevo Usuario!',
            'El Usuario: ' + response.user.nombre + ' ' + response.user.apellido + ', Ha sido ingresado exitosamente!',
            null,
            false,
            1500,
            null,
            'top',
            'row',
            true
          );
          // si el servidor no ha respondido con los parametros correctos (hubo un error), se presenta un mensaje de error
        } else {
          this.status = false;
          this.launchAlert(
            'error',
            'No se ha podido ingresar el Usuario!',
            null,
            null,
            false,
            1500,
            null,
            'top',
            'row',
            true
          );
        }
      }, error => {
        // en caso de que el backend no responda o directamente arroje un error se presenta un mensaje de error
        this.launchAlert(
          'error',
          'No se ha podido ingresar el Usuario!',
          error.error.Message,
          null,
          false,
          1500,
          null,
          'top',
          'row',
          true
        );
        this.status = false;
        console.log(error as any);
      });
  }
  // metodo de actualizacion de los datos del usuario
  update(form) {
    // subscripcion al servicio de usuario para enviar los datos a editar
    this.userService.updateCommons(this.user).subscribe(
      response => {
        if (response.User && response.User._id) {
          this.status = true;
          form.reset();
          this.launchAlert(
            'success',
            'Se ha Editado el Usuario!',
            null,
            null,
            false,
            1500,
            null,
            'top',
            'row',
            true
          );
          this.onEdit = null;
        } else {
          console.log(response);
          this.launchAlert(
            'error',
            'No se ha podido editar el Usuario!',
            null,
            null,
            false,
            1500,
            null,
            'top',
            'row',
            true
          );
        }
      }, error => {
        console.log(error as any);
        this.launchAlert(
          'error',
          'No se ha podido editar el Usuario!',
          error.error.Message,
          null,
          false,
          1500,
          null,
          'top',
          'row',
          true
        );
      });
  }
  // Metodo para conformar la edicion de la cedula
  verifyEditCedula() {
    if (this.cedulaWarning === true) {
      let Message = 'Advertnecia, Modificar la cedula podria ocacionar fallos en el sistema, ';
      Message = Message + 'se recomienda hacerlo solo en casos excepcionales. ';
      Message = Message + 'Queda bajo su responsabilidad los daos ocacionados por esta accion';
      const response = confirm(Message);
      if (response === true) {
        this.cedulaWarning = false;
      } else {
        this.cedulaWarning = true;
      }
    }
  }
  // Metodo para verificar la cedula
  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length >= 10 && cedula.length <=13) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i = i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.ComprobarCedula(cedula)
        } else {
          this.cedulaCorrecta = false;
        }
      }
    }
  }
  ComprobarCedula(cedula) {
    let count = 0;
    for (const almacenada of this.cedulas) {
      almacenada.cedula = almacenada.cedula.replace(/-/gi, '');
      if (almacenada.cedula == cedula) {
        count++
      }
    }
    if (count >= 1) {
      this.cedulaCorrecta = false;
      this.launchAlert('error', 'Cdula Repetida', 'La cdula Ingresada ya Existe', null, false, 3500, false, 'top', 'row', true);
    } else if (count <=0) {
      this.cedulaCorrecta = true;
      this.launchAlert('success', 'Cdula Correcta', 'La cdula Ingresada es Vlida', null, false, 3500, false, 'top', 'row', true);
    }
  }
  // metodo para resetear las variables del formulario
  reset() {
    this.cedulaWarning = null;
  }
  // metodo para cargar las cedulas registradas en el sistema
  initCedulas() {
    this.statisticsService.getCedulas().subscribe(
      response => {
        this.cedulas = response.cedulas;
        console.log(response.Message);
      }
    );
  }
  comprobarCadena(cadena: string, origen: string) {
    let numeros = '0123456789';
    let hasNumbers: boolean = false;
    if (origen == 'nombre') this.nombreCorrecto = true;
    if (origen == 'apellido') this.apellidoCorrecto = true;
    if (origen == 'direccion') this.direccionCorrecta = true;
    if (cadena && cadena.length && cadena.length >= 3 && cadena.length <=24) {
      for (const caracter of cadena) {
        if (numeros.indexOf(caracter) != -1) {
          hasNumbers = true;
        }
      }
      if (origen == 'apellido' && hasNumbers == true) {
        this.launchAlert('error', 'El Apellido no puede contener Numeros', null, null, false, 1500, false, 'top', 'row', true);
        this.apellidoCorrecto = false;
      } else if (origen == 'nombre' && hasNumbers == true) {
        this.launchAlert('error', 'El Nombre no puede contener Numeros', null, null, false, 1500, false, 'top', 'row', true);
        this.nombreCorrecto = false;
      }
    } else if (!cadena.length || cadena.length <=2 ) {
      if (origen == 'nombre') {
        this.launchAlert('error', 'El Nombre es demaciado Corto', null, null, false, 1500, false, 'top', 'row', true);
        this.nombreCorrecto = false;
      }
      if (origen == 'direccion') {
        this.launchAlert('error', 'La Direccion es demaciado Corta', null, null, false, 1500, false, 'top', 'row', true);
        this.direccionCorrecta = false;
      }
      if (origen == 'apellido') {
        this.launchAlert('error', 'El Apellido es demaciado Corto', null, null, false, 1500, false, 'top', 'row', true);
        this.apellidoCorrecto = false;
      }
    } else if (cadena && cadena.length >=25 && origen != 'direccion') {
        if (origen == 'nombre') {
          this.launchAlert('error', 'El Nombre es demaciado Largo', null, null, false, 1500, false, 'top', 'row', true);
          this.nombreCorrecto = false;
        }
        if (origen == 'apellido') {
          this.launchAlert('error', 'El Apellido es demaciado Largo', null, null, false, 1500, false, 'top', 'row', true);
          this.apellidoCorrecto = false;
        }
    } else if (cadena && cadena.length >=49 && origen == 'direccion') {
        this.launchAlert('error', 'La Direccion es demaciado Larga', null, null, false, 1500, false, 'top', 'row', true);
        this.direccionCorrecta = false;
    }
  }
  // metodo plantilla para lanzar las alertas en la interfaz
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
}
