import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { DatosFacturas } from '../../../../models/datosFacturas';
import { DatoFacturaService } from '../../../../service/datoFactura.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-datos-facturas',
  templateUrl: './datos-facturas.component.html',
  styleUrls: ['./datos-facturas.component.css'],
  providers: [DatoFacturaService]
})
export class DatosFacturasComponent implements OnInit {
  public title: string;
  public cargando: boolean;
  public datoFactura: DatosFacturas;
  public today: String;
  public rucCorrecto: boolean;
  public datosFacturas: any[];
  constructor(
    private _location: Location,
    private _datoFacturaService: DatoFacturaService
  ) {
    this.title = 'Cargando';
    this.cargando = true;
    window.document.title = 'MerAgua - Datos de Facturas';
    this.initDatoFactura();
    this.initDatosFacturas();
  }

  initDatosFacturas() {
    this._datoFacturaService.gets().subscribe(
      response => {
        console.log(response);
        this.datosFacturas = response.datoFactura;
      }
    )
  }

  initDatoFactura() {
    const hoy = new Date();
    const year = hoy.getFullYear().toString();
    let month = (hoy.getMonth() + 1).toString();
    if (month.length <= 1) {
      month = '0' + month;
    }
    let day = hoy.getDate().toString();
    if (day.length <= 1) {
      day = '0' + day;
    }
    this.today = day + month + year;
    // this.today = new Date();
    this.datoFactura = {
      _id: null,
      tipoComp: null,
      ruc: null,
      tipoAmb: null,
      serie: null,
      tipEmi: '1',
      codNum: null,
      nombres: null,
      apellidos: null
    }
    this.cargando = false;
    this.initDatosFacturas();
    this.title = 'Ingresar Datos de Facturacin';
  }
  verificarRuc(ruc: string) {
    if (ruc.length == 14) {
      let rucCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      rucCache = ruc.split('');
      for (let i = 0; i < 9 ; i = i + 2) {
        let impar = rucCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = rucCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      console.log('comparador',comparador, ' suma total', sumaTotal);

      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == rucCache[9]) {
          this.rucCorrecto = true;
          console.log('correcto');
          let rucs = ruc.replace(/-/gi, '');
          this.datoFactura.ruc = rucs;
        } else {
          this.rucCorrecto = false;
          console.log('incorrecto');
        }
      }
    } else {
      console.log('incorrecto', ruc.length);
      this.rucCorrecto = null;
    }
  }
   return() {
     this._location.back();
   }
   onSubmit() {
    this._datoFacturaService.save(this.datoFactura).subscribe(
      response => {
        Swal.fire({
          position: 'top-end',
          icon: 'success',
          grow: 'row',
          title: response.Message,
          showConfirmButton: false,
          timer: 1500
        });
      }
    );
   }
  ngOnInit() {
  }

}
