import { Component, OnChanges } from '@angular/core';
import { BillService } from '../../../../../service/bill.service';
import { Bill } from '../../../../../models/bill';
import { ActivatedRoute, Router } from '@angular/router';
import { RegisterService } from '../../../../../service/register.service';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { RateService } from '../../../../../service/rate.service';
import { Rate } from '../../../../../models/rate';
import { ImporteService } from '../../../../../service/importe.service';
import { Location } from '@angular/common';
import { DetalleFacturaService } from '../../../../../service/detalleFactura.service';
import { StatisticsService } from '../../../../../service/statistics.service';
import Swal from 'sweetalert2';
import { DetalleFactura } from '../../../../../models/detalleFactura';
import { NumerosService } from '../../../../../service/numeros.service';


@Component({
  selector: 'app-add-factura',
  templateUrl: './add-factura.component.html',
  styleUrls: ['./add-factura.component.scss'],
  providers: [BillService, NumerosService, StatisticsService, RegisterService, UserService, RateService, ImporteService, DetalleFacturaService]
})

export class AddFacturaComponent implements OnChanges {
  public title: string;
  public usuario: User;
  public bill: Bill;
  public loading = true;
  public registro;
  public tarifa: Rate;
  public detalles: any[];
  public importes;
  public descripcion: any[];
  public fechaActual = new Date;
  public registrada: boolean;
  public noRegister: boolean;
  public numeros: any[];
  public numeroCorrecto: boolean;
  public identity;

  constructor(
    private location: Location,
    private route: ActivatedRoute,
    private billService: BillService,
    private registerService: RegisterService,
    private userService: UserService,
    private rateService: RateService,
    private importeService: ImporteService,
    private detalleService: DetalleFacturaService,
    private router: Router,
    private statisticsService: StatisticsService,
    private numerosService: NumerosService
  ) {
    window.document.title = 'MerAgua - Facturar';
    this.title = 'Cargando....';
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (this.identity.role_user.toLowerCase() == 'operador') this.router.navigate(['/inicio']);
    this.loading = true;
    this.initRegister();
    this.initBill();
    this.detalles = [];
    this.uniqueBill();
    this.initNumeros();
    this.numeroCorrecto = false;
  }
  ngOnChanges() {

  }
  uniqueBill() {
    this.registrada = false;
    this.route.params.subscribe(
      params => {
        const registro = params.id;
        this.billService.getFacturaByRegistro(registro).subscribe(
          response => {
            this.bill = response.factura;
            this.registrada = true;
            console.log(response);
            this.detalleService.getDetalle(this.bill._id).subscribe(
              responseDetalle => {
                console.log(responseDetalle);
                this.detalles = responseDetalle.Detalles;
                this.getLastRange();
                this.asignNumber();
              }
            );
          }
        );
        this.getLastRange();
      }
    );
  }

  initImportes() {
    this.importeService.getImportes().subscribe(
      response => {
        this.importes = response.Importes;
      }, error => {
        console.log(error as any);
      }
    );
  }
  initBill() {
    this.bill = {
      _id: null,
      numero: null,
      fecha: null,
      total: null,
      subtotal: null,
      exceso: null,
      importes: null,
      descuentos: null,
      base: null,
      claveAccesso: null,
      datoFacturado: null,
      pago: null,
      tarifa: null,
      medidor: null,
      usuario: {
        nombre: null,
        cedula: null,
        apellido: null,
        direccion: null,
        telefono: null,
        id: null,
        correo: null
      },
      registro: null,
      cliente: {
        nombre: null,
        cedula: null,
        apellido: null,
        direccion: null,
        telefono: null,
        id: null,
        correo: null
      }
    };
  }
  return() {
    this.location.back();
  }
  initRegister() {
    this.title = 'Facturar Registro del consumo del medidor';
    this.title = 'Registro de la Factura en base al consumo del medidor';
    this.route.params.subscribe(
      params => {
        const id = params.id;
        this.registerService.getOne(id).subscribe(
          response => {
            this.registro = response.Register;
            this.descripcion = [
              {
                nombre: 'Lectura: ' + this.registro.lectura + '. Consumo: ' + this.registro.consumo,
                detalle: 'Tarifa Base',
                valor: '$' + this.registro.base.toFixed(2),
                subtotal: '$' + this.registro.base.toFixed(2)
              },
              {
                nombre: 'Exceso: ' + this.registro.excessConsumo,
                detalle: 'Costo por Metro: $' + this.registro.excessM3.toFixed(2),
                valor: '$' + this.registro.excessCosto.toFixed(2),
                subtotal: '$' + this.registro.subtotal.toFixed(2)
              }
            ];
            this.initUsuario(this.registro.meter.user);
            this.initRate(this.registro.meter.rate);
            this.initImportes();
            this.getTotal();
          }, error => {
            console.error(error as any);
            if (error.status === 404) {
              alert('No se encuentra el registro de consumo!');
              this.noRegister = true;
            }
            this.loading = false;
          }
        );
      }
    );

  }

  date(year, monthIndex): Date {
    let newDate: Date;
    newDate = new Date(year, monthIndex);
    return newDate;
  }

  initRate(id) {
    this.rateService.findRate(id).subscribe(
      response => {
        this.tarifa = response.Rate;
        this.loading = false;
      }, error => {
        console.error(error as any);
        this.loading = false;
      }
    );
  }

  initUsuario(id) {
    this.userService.getUser(id).subscribe(
      response => {
        this.usuario = response.user;
        // this.bill.cli_apellido = this.usuario.apellido;
        // this.bill.cli_nombre = this.usuario.nombre;
        // this.bill.cli_telefono = this.usuario.telefono;
        // this.bill.cli_direccion = this.usuario.direccion;
        // this.bill.cli_cedula = this.usuario.cedula;
        this.fechaActual.getDate();
      }, error => {
        console.error(error as any);
      }
    );
  }

  saveDetalles(detalle) {
    this.detalleService.save(detalle).subscribe(
      responseDetalle => {
        console.log(responseDetalle.Message);
      }
    );
  }
  onSubmit() {
    if (this.bill.pago === null) {
      this.bill.pago = 'debe';
    }
    this.bill.tarifa = this.tarifa._id;
    this.bill.registro = this.registro._id;
    // this.bill.usuario = this.registro.meter.user;
    // this.bill.cli_apellido = this.usuario.apellido;
    // this.bill.cli_nombre = this.usuario.nombre;
    // this.bill.cli_telefono = this.usuario.telefono;
    // this.bill.cli_direccion = this.usuario.direccion;
    // this.bill.cli_cedula = this.usuario.cedula;
    this.bill.fecha = this.fechaActual.toString();
    this.bill.medidor = this.registro.meter._id;
    this.billService.save(this.bill).subscribe(
      response => {
        if (this.detalles.length >= 1) {
          for (const importe of this.detalles) {
            console.log(importe);
            const detalle: DetalleFactura = {
              index: null,
              detalle:     'detalle: ' + this.bill.numero,
              factura:     response.Factura._id,
              importe:     importe._id,
              costo:       importe.costo,
              descuento:   importe.descuento,
              nombre:      importe.nombre,
              percent:     importe.percent,
              subtotal:    importe.subtotal,
              total:       importe.total
            };
            this.saveDetalles(detalle);
            // const detalle: DetalleFactura = {
            //   detalle: 'detalle: ' + this.bill.numero,
            //   factura: response.Factura._id,
            //   importe: importe._id
            // };
          }
        } else if (this.detalles.length <= 1) {
          this.detalles = [
            {
              detalle: 'Sin detalle',
              factura: response.Factura._id,
              importe: null
            }
          ];
          this.saveDetalles(this.detalles[0]);
        }
        this.getLastRange();
        // for (const detalle of this.detalles) {
        // }
        // alert(response.Message);
        this.launchAlert(
          'success',
          'Factura Guardada!',
          'La factura ha sido guardada correctamente!',
          null,
          false,
          1500,
          null,
          'top',
          'top-end',
          true
        );
        this.detalles = [];
        setTimeout(() => {
          this.uniqueBill();
        }, 130);

      }, error => {
        console.error(error as any);
        if ((error.status as any) === 304) {
          // alert('La factura ingresada ya existe');
          this.launchAlert(
            'error',
            'Error al Facturar',
            'La factura ingresada ya existe',
            null,
            false,
            1500,
            null,
            'top',
            'top-end',
            true
          );
        } else {
          // alert('Error al ingresar la factura');
          this.launchAlert(
            'error',
            'Error al Facturar',
            'Se ha producido un error al ingresar la factura!',
            null,
            false,
            1500,
            null,
            'top',
            'top-end',
            true
          );
        }
      }
    );

  }
  public lastRange;
  getLastRange() {
    this.numerosService.showRaw().subscribe(
      response => {
        console.log(response);
        const reorden = response.Numeros.sort((a,b) => (b.num_min - a.num_min));
        this.lastRange = reorden[0];
        this.asignNumber();
      }
    );
  }
  asignNumber() {
    // this.numeros
    let numeroNuevo = this.lastRange.num_min;
    let repetido = true;
    console.log('numeros: ', this.numeros);
    if (this.numeros && this.numeros.length >= 1) {
      this.numeros.sort();
      while (repetido == true) {
        for (const numeros of this.numeros) {
          if (numeros == numeroNuevo) {
            numeroNuevo++;
          } else {
            if (this.bill._id != null) {
              this.bill.numero = numeroNuevo;
            }
            this.numeroCorrecto = true;
            repetido = false;
          }
          console.log(numeros);
        }
      }
    } else {
      console.log('sin numeros', this.bill._id);
      if (this.bill._id != null) {
        this.bill.numero = numeroNuevo;
        this.numeroCorrecto = true;
      }
    }

  }
  getTotal() {
    this.bill.total = this.registro.subtotal;
    for (const detalle of this.detalles) {
      if (detalle.percent === false) {
        if (detalle.descuento === true) { this.bill.total = this.bill.total - detalle.costo; }
        if (detalle.descuento === false) { this.bill.total = this.bill.total + detalle.costo; }
        detalle.total = this.bill.total;
      } else if (detalle.percent === true) {
        detalle.subtotal = (detalle.costo / 100) * this.bill.total;
        if (detalle.descuento === true) { detalle.total = this.bill.total - detalle.subtotal; }
        if (detalle.descuento === false) { detalle.total = this.bill.total + detalle.subtotal; }
        this.bill.total = detalle.total;
      }
    }
    this.bill.total = parseFloat(this.bill.total.toFixed(2));


  }
  onDeleteImport(id) {
    for (let i = 0; i < this.detalles.length; i++) {
      const detalle = this.detalles[i];
      if (detalle._id === id) {
        this.importes.push(detalle);
        this.detalles.splice(i, 1);
        this.getTotal();
      }
    }
  }

  onAddImporte(id) {
    for (let i = 0; i < this.importes.length; i++) {
      const importe = this.importes[i];
      if (importe._id === id) {
        this.detalles.push(importe);
        this.detalles.sort((a, b) => {
          if (a.descuento > b.descuento) {
            return 1;
          }
          if (a.descuento < b.descuento) {
            return -1;
          }
          return 0;
        });
        this.importes.splice(i, 1);
        this.getTotal();
      }
    }
  }

  onPay() {
    console.log(this.bill);
    this.billService.pay(this.bill).subscribe(
      response => {
        console.log(this.bill);
        localStorage.setItem('factura', this.bill._id);
        // alert(response.Message);
        this.launchAlert(
          'success',
          'Factura Guardada Correctamente!',
          response.Message,
          null,
          true,
          1500,
          null,
          'center',
          null,
          true
        );
        window.open('factura');
        this.router.navigate(['/facturar']);
      }, error => {
        console.error(error as any);
      }
    );
    console.log('pagando factura');
  }
  initNumeros() {
    this.numeros = [];
    this.statisticsService.getBillsNumbers().subscribe(
      response => {
        if (response.numeros) {
        for (const factura of response.numeros) {
          if (factura.numero != null) {
            this.numeros.push(factura.numero);
          }
        }
        } else if (!response.numeros) {
          this.numeros = [{
              numero: 0
          }];
        }
        console.log(response.Message);
      }
    );
  }

  verifyNumber(numeroActual) {
    let count = 0;
    if (this.numeros.length >= 1) {
      for (const numero of this.numeros) {
        if (numeroActual == numero.numero) {
          count++;
        }
      }
    }
    if (count >= 1 && numeroActual != '') {
      console.log('lanzando alerta?');
      this.launchAlert(
        'error',
        'Numero Repetido!',
        'El numero de la factura ya se ingreso anteriormente en el sistema, por favor, compruebe el numero e intentelo nuevamente',
        null,
        false,
        1500,
        null,
        'top',
        'row',
        true
      );
      this.numeroCorrecto = false;
    } else if (count === 0  && numeroActual != '') {
      this.numeroCorrecto = true;
      this.launchAlert(
        'success',
        'Numero Correcto!',
        'Numero de la Factura nuevo!',
        null,
        false,
        1500,
        null,
        'top',
        'row',
        true
      );
    }
  }




  // |||\\\        |||
  // ||| \\\       |||
  // |||  \\\      |||
  // |||  \\\      |||
  // |||   \\\     |||
  // |||====\\\    |||
  // |||    \\\    |||
  // |||     \\\   |||      //
  // |||      \\\  ||||||||||

  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
}
