// tslint:disable: triple-equals
// tslint:disable: curly
import { Component, OnInit, DoCheck } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
// Servicio de Usuario //
import { trigger, style, transition, animate, state } from '@angular/animations';
import { UserService } from '../../../../../service/user.service';
import { Global } from '../../../../../service/global';
import { User } from '../../../../../models/user';
import { Meter } from '../../../../../models/meter';
import { MeterService } from '../../../../../service/meter.service';
import { Location } from '@angular/common';
import { BillService } from '../../../../../service/bill.service';
import Swal from 'sweetalert2';
import { DetalleFacturaService } from '../../../../../service/detalleFactura.service';


@Component({
  selector: 'profileComponent',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
  providers: [UserService, MeterService, BillService, DetalleFacturaService],
  animations: [
    trigger('enterState', [
      state('void', style({
        transform: 'translateX(-100%)',
        opacity: 0
      })),
      transition(':enter', [
        animate(300, style({
          transform: 'translateX(0)',
          opacity: 1
        }))
      ])
    ])
  ]
})
export class ProfileComponent implements OnInit, DoCheck {
  public title: string;
  public identity: any;
  public loading: boolean;
  public image: string;
  public url: string;
  public user: User;
  public meterDetail: Meter;
  public meters: Meter[] = [];
  public age;
  public showingDetails: boolean;
  public bills: any[];
  public sinFacturas: boolean;
  public fromProfile: boolean;
  public loadingFacturas: boolean;
  public loadingMedidores: boolean;
  constructor(
    private location: Location,
    private userService: UserService,
    private route: ActivatedRoute,
    private meterService: MeterService,
    private billService: BillService,
    private detailService: DetalleFacturaService
  ) {
    window.document.title = 'MerAgua - Perfil de Usuario';
    this.loading = true;
    this.loadingFacturas = true;
    this.loadingMedidores = true;
    this.title = 'Cargando.....';
    this.showingDetails = false;
    this.url = Global.url + 'user/';
  }
  ngOnInit() {
    this.identity = this.userService.getIdentity();
    if (this.identity.role_user.toLowerCase() == 'operador') {
      this.fromProfile = true;
    }
    this.initUser();
  }
  ngDoCheck() {
    // this.identity = this.userService.getIdentity();
  }

  return() {
    this.location.back();
  }

  initUser() {
    this.route.params.subscribe(
      params => {
        let id: string;

        if (localStorage.getItem('user') != null || localStorage.getItem('user') != undefined) id = localStorage.getItem('user');
        if (params.id) id = params.id;
        if (id != undefined) {
          this.userService.getUser(id).subscribe(
            response => {
              if (response.user) {
                this.user = response.user;
                this.title = 'Perfil del Usuario ';
                this.initMedidores(id);
                this.initFacturas(id);
                console.log(this.user);
                if (this.user.fecha_nacimiento) {
                  const convertAge = new Date(this.user.fecha_nacimiento);
                  const timeDiff = Math.abs(Date.now() - convertAge.getTime());
                  this.age = Math.floor((timeDiff / (1000 * 3600 * 24)) / 365);
                  console.log(this.age);

                }
              } else {
                this.title = 'No se encuentra el perfil del usuario';
                this.loading = false;
                alert('no se encuentra el usuario....');
              }
            }, error => {
              this.loading = false;
              this.title = 'Error al buscar el usuario';
              console.log(error as any);
            });
        } else {
          this.loading = false;
        }

      }
    );
  }
  getTitle(bill): string {
    const title = 'Facura: ' + bill.numero + ', del medidor: ' + bill.medidor.clave + ', con fecha de: ';
    return title
  }
  getRegisterDate(year, month): Date {
    const date: Date = new Date(year, month);
    return date
  }
  initFacturas(id) {
    this.billService.getFacturaByUser(id).subscribe(
      response => {
        console.log(response);
        if (response.bills) {
          this.bills = response.bills;
          this.sinFacturas = false;
          this.initDetails();
        } else {
          this.sinFacturas = true;this.loadingFacturas = false;
        }
        this.launchAlert(
          'success',
          'Carga de Facturas Exitosa!',
          response.Message,
          null,
          false,
          3500,
          false,
          'top-end',
          'row',
          true
        );
      }, error => {
        console.log(error as any);
      }
    );
  }
  initDetails() {
    for (let bill of this.bills) {
      const id = bill._id;
      let details: any[];
      bill.descripcion = [
        {
          nombre: 'Lectura: ' + bill.registro.lectura + '. Consumo: ' + bill.registro.consumo,
          detalle: 'Tarifa Base',
          costo: null,
          valor: bill.registro.base,
          subtotal: bill.registro.base
        },
        {
          nombre: 'Exceso: ' + bill.registro.excessConsumo,
          detalle: 'Costo por m:',
          costo: bill.registro.excessM3,
          valor: bill.registro.excessCosto,
          subtotal: bill.registro.subtotal.toFixed(2)
        }
      ];
      this.detailService.getDetalle(id).subscribe(
        response => {
          if (response.Detalles) {
            details = response.Detalles;
            bill.detalles = details;
          }
        }
      );
    }
    console.log(this.bills);
    this.loadingFacturas = false;
  }
  initMedidores(id) {
    this.meterService.getUsersMeters(id).subscribe(
      response => {
        this.meters = response.Medidores;
        // this.meters.sort((a, b) => (a.clave - b.clave));
        this.loading = false;
        console.log(this.meters);
      }, error => {
        console.error(error as any);
        this.loading = false;
      }
    );
  }

  viewDetails(meter) {
    this.meterDetail = meter;
    this.showingDetails = true;
  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
  recortarNombre(usuario): string{
    let nombres: string;
    let nombre = usuario.nombre.split(/ /gi, 1);
    let apellido = usuario.apellido.split(/ /gi, 1);
    nombres = apellido + ' ' + nombre;
    return nombres;
  }
}
