import { Component, DoCheck } from '@angular/core';

@Component({
    selector: 'app-side-bar-menu',
    templateUrl: './sidebarmenu.component.html',
    styleUrls: ['./sidebarmenu.component.scss']
})
export class SideBarMenuComponent implements DoCheck {
    public title: string;
    public identity;
    public lastBill: boolean;
    public url: string;
    constructor() {
        this.title = 'SidebarMenu of LTE Works!';
        this.lastBill = false;
        this.url = 'http://localhost:4200/';
        this.identity = JSON.parse(localStorage.getItem('identity'));
    }
    ngDoCheck() {
        if (localStorage.getItem('factura') &&
        localStorage.getItem('factura') !== '' &&
        localStorage.getItem('factura') !== null &&
        localStorage.getItem('factura') !== undefined) {
            this.lastBill = true;
        } else {
            this.lastBill = false;
        }
    }
}
