import { Component, OnInit } from '@angular/core';
import { MeterService } from '../../../../../service/meter.service';
import { Meter } from '../../../../../models/meter';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { RateService } from '../../../../../service/rate.service';
import { Global } from '../../../../../service/global';
import { UploadService } from '../../../../../service/upload.service';
import { Location } from '@angular/common';
import { SectorService } from '../../../../../service/sector.service';
import { Sector } from '../../../../../models/sector';
import Swal from 'sweetalert2';
import { StatisticsService } from '../../../../../service/statistics.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-add-medidor',
  templateUrl: './addmedidor.component.html',
  styleUrls: ['./addmedidor.component.scss'],
  providers: [MeterService, RateService, UserService, UploadService, SectorService, StatisticsService]

})
export class AddMedidorComponent implements OnInit {
  public title: string;
  public medidor: Meter;
  public users: User[] = [];
  public usuarios;
  public tarifas;
  public identity;
  public status: boolean;
  public token;
  public conteos;
  public user: any;
  public url: string;
  public medidorOk: boolean;
  public medidores;
  public filesToUpload: Array<File>;
  public image: string;
  public sectores: Sector[];
  public claves;
  constructor(
    private location: Location,
    private meterService: MeterService,
    private userService: UserService,
    private rateService: RateService,
    private uploadService: UploadService,
    private _sectorService: SectorService,
    private _statisticsService: StatisticsService,
    private router: Router
  ) {
    this.identity = JSON.parse(localStorage.getItem('identity'));
    window.document.title = 'MerAgua - Agregar Medidor';
    if (this.identity.role_user.toLowerCase() == 'operador') this.router.navigate(['/inicio']);
    this.title = 'Agregar Nuevo Medidor';
    this.token = this.userService.getToken();
    this.url = Global.url + 'meter/';
    this.initMedidor();
    this.InitUser();
    this.InitSectores();
    this.InitClaves();
  }
  initMedidor() {
    this.medidor = new Meter(
      '',                 // id
      null,               // clave
      '',                 // direccion
      '',                 // user
      '',                 // rate
      '',                 //
      ''
    );
  }
  goRandom() {
    // let chars = '0123456789';
    // let size = 10;
    // let cadena = '';
    Swal.fire({
      title: 'Generando Clave aleatoria',
      icon: 'info',
      showConfirmButton: false,
      position: 'top',
      grow: 'row',
      toast: true,
      backdrop: false
    });
    const max = 9999999999;
    const min = 999999;
    let cadena =  Math.random() * (max - min) + min;
    cadena = parseInt(cadena.toFixed(0), 0);
    this.validarMedidor((cadena));
    this.medidor.clave = cadena;
  }
  InitClaves() {
    this._statisticsService.getMetersNumbers().subscribe(
      response => {
        if (response.Claves) this.claves = response.Claves;
        if (!response.Claves) this.launchAlert('warning', response.Message, null, null, false, 1500, false, 'top', 'row', true);
      }
    );
  }
  InitSectores() {
    this._sectorService.getSectores().subscribe(
      response => {
        this.sectores = [];
        for (const sector of response.sectores) {
          if (sector.activa) {
            this.sectores.push(sector);
          }
        }
      }, error => {
        console.error(error as any);
      }
    );
  }
  InitUser() {
    this.user = {
      _id: '',
      cuenta: '',
      contrase: '',
      correo: '',
      nombre: '',
      image: '',
      apellido: '',
      cedula: '',
      direccion: '',
      telefono: '',
      fecha_nacimiento: '',
      sexo: '',
      role_user: '',
      activa: true,
      gettoken: ''
    };
  }
  ngOnInit() {
    this.cargarDatos();
  }
  return() {
    this.location.back();
  }
  cargarDatos() {
    this.getUsers();
    this.getRates();
    this.getMeters();
  }
  getMeters() {
    this.meterService.showMeters().subscribe(
      response => {
        this.medidores = response.Medidores;
      }, error => {
        console.error(error as any);

      }
    );
  }
  getUsers() {
    this.userService.showRaw().subscribe(
      response => {
        this.usuarios = response.Usuarios;
        // if (this.users.length <=0) {
        this.users = response.Usuarios;
        // }
      }, error => {
        console.log(error as any);
      }
    );
  }
  getRates() {
    this.rateService.getRate().subscribe(
      response => {
        console.log(response);

        this.tarifas = response.Rates;
      }, error => {
        console.log(error as any);
      }
    );
  }
  busquedaUsuario(termino, porNombre, porCedula) {
    let busqueda;
    termino = termino.toLowerCase();
    let conteo = +0;
    this.conteos = null;
    this.users = [];
    this.usuarios.forEach(item => {
      if (porNombre === true && porCedula === true) {
        busqueda = item.nombre.replace(/ /g, '').toLowerCase();
        busqueda = busqueda + '' + item.apellido.replace(/ /g, '').toLowerCase();
        busqueda = busqueda + item.cedula.replace('-', '');
      } else if (porNombre === true && porCedula === false) {
        busqueda = item.nombre.replace(/ /g, '').toLowerCase() + '' + item.apellido.replace(/ /g, '').toLowerCase();
      } else if (porNombre === false && porCedula === true) {
        busqueda = item.cedula.replace('-', '');
        termino = termino.replace('-', '');
      }
      if (porNombre === false && porCedula === false) {
        this.getUsers();
      } else {
        if (busqueda.indexOf(termino, 0) >= 0) {
          this.users.push(item);
          conteo = conteo + 1;
        }
      }
    });
    if (this.users.length <= 0) {
      this.getUsers();
      conteo = 0;
      console.log(conteo);
    }
    this.conteos = conteo;
  }

  validarMedidor(clave) {
    this.InitClaves();
    if (clave !== undefined) {
      if (clave.length <= 5 || clave.length >= 11) {
        this.medidorOk = null;
        let message: string;
        if (clave.length <= 5) message = 'Es necesario Ingresar al menos 6 dgitos.';
        if (clave.length >= 10) message = 'la clave no puede exceder los 10 dgitos.';
        this.launchAlert('error', 'Clave Incorrecta, ', message, null, false, 1500, false, 'top', 'row', true);
      } else if (clave.length >= 6 && clave.length <= 10) {
        let count = 0;
        if (this.claves && this.claves.length >= 1) {
          for (const medidor of this.claves) {
            console.log(medidor);
            if (clave == medidor.clave) {
              count++;
            }
          }
        } else {
          console.log(this.claves);
        }
        if (count >= 1) {
          this.medidorOk = false;
          this.launchAlert('error', 'Clave Incorrecta, ',
            'Clave de medidor repetida: Ya existe un medidor con esa clave!',
            null, false, 1500, false, 'top', 'row', true);
            console.log('clave incorrecta');
        } else if (count === 0) {
          Swal.fire({
            title: 'Clave correcta!',
            icon: 'success',
            showConfirmButton: false,
            position: 'top',
            grow: 'row',
            toast: true,
            backdrop: false
          })
          this.medidorOk = true;
          console.log('clave correcta');
        }
      }
    }
  }

  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
  // generarClave(): string {
  //   var chars = '0123456789ABCDEF';
  //   var code = '';
  //   for (let index = 0; index < 7; index++) {
  //     var rand = Math.floor(Math.random()*chars.length);
  //     code += chars.substr(rand,1);
  //   }
  //   this.medidor.clave=code;
  //   console.log(code);
  //   return code;
  // }
  onSubmit(form) {
    this.meterService.save(this.token, this.medidor).subscribe(
      response => {
        if (response.meter && response.meter._id) {
          this.status = true;
          this.title = 'Medidor Agregado Correctamente';
          if (this.filesToUpload) {
            this.uploadService.makeFileRequest(this.url + 'image/' + response.meter._id, [], this.filesToUpload, this.token, 'image')
              .then((result: any) => {
                console.log(result);
              }).catch((error: any) => {
                console.log(error);
              });
          }
          this.launchAlert('success', 'Exito al ingresar el Medidor' + response.meter.clave, null, null, null, 3500, false, 'top', 'row', true); this.initMedidor();
          this.medidorOk = null;
          this.getMeters();
          setTimeout(() => this.title = 'Agregar Nuevo Medidor', 5000);
          form.form.reset();
        } else {
          this.status = false;
        }
      }, error => {
        this.status = false;
        alert(error.error.message);
        console.log('error');
        console.log(error as any);
      }
    );
  }
  formReset(form) {
    form.reset();
  }
  onSubmitUser(form) {
    this.user = new User(
      '',    // "_id"
      null,    // "cuenta"
      null,    // "contrase"
      null,    // "correo"
      form.controls.nombre.value,    // "nombre"
      '',    // "image"
      form.controls.apellido.value,    // "apellido"
      form.controls.cedula.value,    // "cedula"
      form.controls.direccion.value,    // "direccion"
      '',
      '',    // "fecha_nacimiento"
      form.controls.sexo.value,    // "sexo"
      'Simple',    // "role_user"
      true,    // "activa"
      '',
    );
    console.log(this.user);
    this.userService.registerSimple(this.user).subscribe(
      response => {
        alert(response.Message);
        console.log(response);
        form.reset();
      }, error => {
        alert(error.error.Message);
        console.log(error as any);
      }
    );
  }
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = fileInput.target.files as Array<File>;
    console.log(this.filesToUpload);
  }

}
