import { Component, OnInit, DoCheck, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../../../service/user.service';
import Swal from 'sweetalert2';
import { StatisticsService } from '../../../../service/statistics.service';
import { BillService } from '../../../../service/bill.service';
import { Location } from '@angular/common';
import { MetaDefinition } from '@angular/platform-browser';
import { MetatagService } from '../../../../service/metatag.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  providers: [UserService, StatisticsService, BillService, MetatagService]
})

export class HomeComponent implements OnInit, OnChanges {
  public logo: string;
  public identity;
  public statistics;
  public facturaChart;
  public barChartData = [
    { data: [65, 23, 422, 123, 4, 445], label: 'Series A' },
    { data: [555, 223, 42, 143, 55, 45], label: 'Series B' },
    { data: [125, 255, 44, 244, 3, 25], label: 'Series C' }
  ];
  public barChartLabels = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartOptions = {
    scaleShowVerticalLines: false,
    responsive: true,
  };
  public barChartLegend = true;
  public barChartType = 'bar';
  constructor(
    private userService: UserService,
    private router: Router,
    private _statisticService: StatisticsService,
    private _billService: BillService,
    private location: Location,
    private _metatagServide: MetatagService
  ) {
    window.document.title = 'MerAgua - Inicio';
    this.facturaChart = {
      barChartData: [],
      barChartLabels: [],
      barChartOptions: {
        responsive: true
      },
      barChartLegend: true,
      barChartType: null
    };
    this.logo = '../../../assets/resources/home-logo/logo large w-bg.png';
    this.initStatistics();
    this.initFacturaChart();

  }
  setMetaTags() {
    this._metatagServide.getRouteData().subscribe(
      data => {
        console.log(data);

      }
    )
  }
  return() {
    this.location.back();
  }
  initFacturaChart() {
    this._billService.getAll().subscribe(
      response => {
        console.log(response.Bill);
        let facturas = [{
          tipo: 'debe',
          facturas: []
        }];
        facturas.push({ tipo: 'efectivo', facturas: [] });
        facturas.push({ tipo: 'tarjeta', facturas: [] });
        facturas.push({ tipo: 'transferencia', facturas: [] });
        let counters = [];
        for (const pago of facturas) {
          let count = 0;
          for (const factura of response.Bill) {
            if (factura.pago == pago.tipo) {
              pago.facturas.push(factura);
              count++;
            }
          }
          pago.tipo = pago.tipo.toLocaleUpperCase() + ': ' + count;
          counters.push(count);
          this.facturaChart.barChartLabels.push(pago.tipo);
        }
        this.facturaChart.barChartData.push({ data: counters, label: 'Facturas'});
        // this.facturaChart.barChartData = [
        //   { data: [65, 23, 422, 123, 4, 445], label: 'Series A' },
        //   { data: [555, 223, 42, 143, 55, 45], label: 'Series B' },
        //   { data: [125, 255, 44, 244, 3, 25], label: 'Series C' }
        // ];
        this.facturaChart.barChartType = 'polarArea';
        console.log('opciones del chart', this.facturaChart);
      }
    );
  }
  verChartFactura() {
    if (this.facturaChart.barChartType == 'polarArea') {
      this.facturaChart.barChartType = 'radar';
    } else if (this.facturaChart.barChartType == 'radar') {
      this.facturaChart.barChartType = 'polarArea';
    }
  }
  public count = 0;
  changeType() {
    if (this.count == 0) {
      this.barChartType = 'pie';
      this.count++;
    } else if (this.count == 1) {
      this.barChartType = 'line';
      this.count++;
    } else if (this.count == 2) {
      this.barChartType = 'doughnut';
      this.count++;
    } else if (this.count == 3) {
      this.barChartType = 'radar';
      this.count++;
    } else if (this.count == 4) {
      this.barChartType = 'polarArea';
      this.count++;
    } else if (this.count == 5) {
      this.barChartType = 'bar';
      this.count = 0;
    }
  }
  ngOnChanges(): void {
    this.initStatistics();
  }
  ngOnInit() {
    this.identity = this.userService.getIdentity();
    if (this.identity === null || this.identity === undefined || this.identity === 'null' || this.identity === 'undefined') {
      this.router.navigate(['/login']);
    }
  }
  initStatistics() {
    this._statisticService.getGlobals().subscribe(
      response => {
        console.log(response);
        this.statistics = response.statistics;
      }
    )
  }
  launchModal() {
    Swal.fire(
      'Good job!',
      'You clicked the button!',
      'success'
    );
  }
}
