import { Component, Output, EventEmitter, OnInit } from "@angular/core";
import { ImporteService } from '../../../../../service/importe.service';
import { Importe } from '../../../../../models/importe';


@Component({
    selector: 'app-show-importes',
    templateUrl: './showimportes.component.html',
    styleUrls: ['./showimportes.component.scss'],
    providers: [ImporteService]
})

export class ShowImportesComponent implements OnInit {
    public importes: Importe[]=[];
    public identity = localStorage.getItem('identity');
    @Output() importEdit= new EventEmitter();

    constructor(
        private importeService: ImporteService
    ) {
    }
    ngOnInit() {
        this.initImportes();

    }
    initImportes() {
        console.log('cargando datos....');
        
        this.importeService.getImportes().subscribe(
            response => {
                this.importes = response.Importes;
            }, error => {
                console.log(error as any);
            }
        )
    }
    onEdit(importe) {
        console.log(importe);
        importe = JSON.stringify(importe)
        this.importEdit.emit(importe);
    }
    tituloImporte(importe): string {
        let title = '';
        if (importe.descuento == true) {
            title = 'Descuento del Total'
        } else if (importe.descuento == false) {
            title = 'Importe Agregado'
        }
        return title;
    }
}