// tslint:disable: curly
// tslint:disable: variable-name
import { Component } from '@angular/core';
// Servicio de Usuario //
import { UserService } from '../../../../../service/user.service';
import { Location } from '@angular/common';
import { StatisticsService } from '../../../../../service/statistics.service';
import { trigger, style, transition, animate, state } from '@angular/animations';
import { Global } from '../../../../../service/global';
import { Meter } from '../../../../../models/meter';
import { MeterService } from '../../../../../service/meter.service';
import { User } from '../../../../../models/user';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.scss'],
  providers: [UserService, StatisticsService, MeterService],
  animations: [
    trigger('enterState', [
      state('void', style({
        transform: 'translateX(-100%)',
        opacity: 0
      })),
      transition(':enter', [
        animate(300, style({
          transform: 'translateX(0)',
          opacity: 1
        }))
      ])
    ])
  ]
})
export class MyProfileComponent {
  public title: string;
  public identity: User;
  public loading: boolean;
  public statistics;
  public bufferUser: User;
  public url: string;
  public mediaCosto: number;
  public mayorCosto: number;
  public menorCosto: number;
  public meterDetail: Meter;
  public showingDetails: boolean;
  public meters: Meter[] = [];
  public age;
  constructor(
    private _location: Location,
    private _statisticsService: StatisticsService,
    private _meterService: MeterService,
    private _userService: UserService,
    private _router: Router
  ) {
    this.title = 'Perfil de Empleado';
    window.document.title = 'MerAgua - Mi Perfil';
    this.url = Global.url + 'user/';
    this.identity = JSON.parse(localStorage.getItem('identity'));
    this.bufferUser = JSON.parse(localStorage.getItem('identity'));
    if (this.identity.fecha_nacimiento) {
      const convertAge = new Date(this.identity.fecha_nacimiento);
      const timeDiff = Math.abs(Date.now() - convertAge.getTime());
      this.age = Math.floor((timeDiff / (1000 * 3600 * 24)) / 365);
      console.log(this.age);

    }
    this.initStatistics(this.identity._id);
  }

  initStatistics(id) {
    this._statisticsService.getUserStatistics(id).subscribe(
      response => {
        console.log(response);
        this.statistics = response;
      }
    );
  }
  return() {
    this._location.back();
  }
  desactivarCuenta(pass){
    const userpass: any = {
      contrase : pass
    };
    this._userService.passwordConfirm(userpass).subscribe(
      response => {
        console.log(response);
        if (response.check == true) {
          let user: any;
          if (this.identity.activa == true) user = {activa: false};
          if (this.identity.activa == false) user = {activa: true};
          this._userService.changeActiva(this.identity, this.identity._id).subscribe(
            response => {
              this.launchAlert(
                'success',
                'Estado del usuario Cambiado!',
                response.Message,
                null,
                false,
                1500,
                null,
                'top',
                'row',
                true
              );
              this.identity = response.user;
              localStorage.clear();
              this._router.navigate(['/login']);
              console.log(this.identity);
            }
          );
        }
      }
    );
  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
}
