import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';
import { DatosFacturas } from '../models/datosFacturas';


@Injectable()

export class DatoFacturaService {
  public url: string;
  constructor(
    private http: HttpClient
  ) {
    this.url = Global.url + 'facturacion/';
  }
  save(datoFactura: DatosFacturas): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const params = JSON.stringify(datoFactura);
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.post(this.url + 'save', params, { headers });
  }
  gets(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
      .set('Authorization', token);
    return this.http.get(this.url + 'gets', { headers });

  }
}
