// tslint:disable: variable-name
// tslint:disable: curly
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BillService } from '../../../../../service/bill.service';
import * as moment from 'moment';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-reporte-facturas',
  templateUrl: './reporte-facturas.component.html',
  styleUrls: ['./reporte-facturas.component.css'],
  providers: [BillService]
})
export class ReporteFacturasComponent implements OnInit {
  public identity;
  public facturas;
  public bufferFacturas;
  public bufferFacturasByDate;
  public verNoPagadas: boolean;
  public byEmit: boolean;
  public byCons: boolean;
  public byMeter: boolean;
  public byNum: boolean;
  public byUser: boolean;
  public byVal: boolean;
  public byOrd: boolean;
  public desde;
  public hasta;
  public validDate: boolean;
  public onCons: boolean;
  public total: number;
  constructor(
    private _router: Router,
    private _billService: BillService
  ) {
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (!this.identity) this._router.navigate(['/home']);
    this.verNoPagadas = true;
    this.byEmit = false;
    this.byCons = false;
    window.document.title = 'MerAgua - Reportes';
    this.validDate = false;
    this.byMeter = false;
    this.byNum = false;
    this.byUser = false;
    this.byVal = false;
    this.byOrd = false;
    this.onCons = false;
    this.initFacturas();
  }
  switchDate() {
    this.validateDate();
  }
  getTotales() {
    this.total = 0
    for (const factura of this.facturas) {
      this.total = this.total + factura.total;
    }
  }
  altPayed() {
    if (!this.verNoPagadas) {
      this.facturas = []
      for (const factura of this.bufferFacturas) {
        if (factura.numero && factura.numero >= 1) this.facturas.push(factura);
      }
    } else if (this.verNoPagadas) {
      this.facturas = this.bufferFacturas;
    }
    this.bufferFacturasByDate = this.facturas;
    this.getIndex();
  }
  validateDate() {
    if (this.desde && this.hasta) {
      const fechaDesde = moment(this.desde);
      const fechaHasta = moment(this.hasta);
      const difference = fechaHasta.diff(fechaDesde, 'days');
      if (difference < 0) {
        this.validDate = false;
        this.launchAlert(
          'error',
          'Error, la Fecha de inicio no puede superar a la Fecha de fin',
          null,
          null,
          null,
          3500,
          false,
          'top',
          'row',
          true
        );
      } else {
        this.validDate = true;
        this.filterByDate();
      }
    }
  }
  filterByDate() {
    if (this.onCons) {
      console.log('por consumo');
    } else {
      console.log('por emision');
    }
    this.bufferFacturasByDate = this.facturas;
    this.facturas = [];
    for (const factura of this.bufferFacturas) {
      const fechaDesde = moment(this.desde);
      const fechaHasta = moment(this.hasta);
      let fechaFactura;
      let diffMin;
      let diffMax;
      if (!this.onCons) {
        fechaFactura = moment(factura.fecha);
        diffMin = fechaFactura.diff(fechaDesde, 'days');
        diffMax = fechaFactura.diff(fechaHasta, 'days');
      } else if (this.onCons) {
        fechaFactura = moment((factura.registro.month + 1) + '-01-' + factura.registro.year);
        diffMin = fechaFactura.diff(fechaDesde, 'months');
        diffMax = fechaFactura.diff(fechaHasta, 'months');
      }
      if (diffMin >=0 && diffMax <= 0) {
        this.facturas.push(factura);
      }
    }
    this.getTotales();
  }
  orderByMeter() {
    this.facturas.sort((a, b) => {
      let aM;
      let bM;
      if (this.byMeter) {
        aM = parseInt(a.medidor.clave);
        bM = parseInt(b.medidor.clave);
      } else if (!this.byMeter) {
        bM = parseInt(a.medidor.clave);
        aM = parseInt(b.medidor.clave);
      }
      if (aM > bM) return -1;
      if (aM < bM) return 1;
      if (aM == bM) return 0;
    });
    if (this.byMeter) this.byMeter = false; else
      if (!this.byMeter) this.byMeter = true;
    this.getIndex();
  }
  orderByNumber() {
    this.facturas.sort((a, b) => {
      let aM;
      let bM;
      if (!a.numero || a.numero == null) a.numero = 0;
      if (!b.numero || b.numero == null) b.numero = 0;
      if (this.byNum) {
        aM = parseInt(a.numero);
        bM = parseInt(b.numero);
      } else if (!this.byNum) {
        bM = parseInt(a.numero);
        aM = parseInt(b.numero);
      }
      if (aM > bM) return -1;
      if (aM < bM) return 1;
      if (aM == bM) return 0;
    });
    if (this.byNum) this.byNum = false; else
      if (!this.byNum) this.byNum = true;
    this.getIndex();
  }
  orderByUser() {
    this.facturas.sort((a, b) => {
      let aM;
      let bM;
      if (this.byUser) {
        aM = a.usuario.nombre;
        bM = b.usuario.nombre;
      } else if (!this.byUser) {
        bM = a.usuario.nombre;
        aM = b.usuario.nombre;
      }
      if (aM > bM) return -1;
      if (aM < bM) return 1;
      if (aM == bM) return 0;
    });
    if (this.byUser) this.byUser = false; else
      if (!this.byUser) this.byUser = true;
    this.getIndex();
  }
  orderByValue() {
    this.facturas.sort((a, b) => {
      let aM;
      let bM;
      if (this.byVal) {
        aM = parseInt(a.total);
        bM = parseInt(b.total);
      } else if (!this.byVal) {
        bM = parseInt(a.total);
        aM = parseInt(b.total);
      }
      if (aM > bM) return -1;
      if (aM < bM) return 1;
      if (aM == bM) return 0;
    });
    if (this.byVal) this.byVal = false; else
      if (!this.byVal) this.byVal = true;
    this.getIndex();
  }
  orderByEmitt() {
    this.facturas.sort((a, b) => {
      let aM;
      let bM;
      let diff: number;
      if (this.byEmit) {
        aM = moment(a.fecha);
        bM = moment(b.fecha);
      } else if (!this.byEmit) {
        aM = moment(b.fecha);
        bM = moment(a.fecha);
      }
      diff = aM.diff(bM, 'seconds');
      if (diff >= 1) return -1;
      if (diff <= -1) return 1;
      if (diff == 0) return 0;
    });
    if (this.byEmit) this.byEmit = false; else
      if (!this.byEmit) this.byEmit = true;
    this.getIndex();
  }
  orderByCons() {
    this.facturas.sort((a, b) => {
      let aM;
      let bM;
      let diff: number;
      const fecha1String = (a.registro.month + 1) + '-01-' + a.registro.year;
      const fecha2String = (b.registro.month + 1) + '-01-' + b.registro.year;
      if (this.byCons) {
        aM = moment(fecha1String);
        bM = moment(fecha2String);
      } else if (!this.byCons) {
        aM = moment(fecha2String);
        bM = moment(fecha1String);
      }
      diff = aM.diff(bM, 'months');
      if (diff >= 1) return -1;
      if (diff <= -1) return 1;
      if (diff == 0) return 0;
    });
    if (this.byCons) this.byCons = false; else
      if (!this.byCons) this.byCons = true;
    this.getIndex();
  }
  getDate(month, year): string {
    month++;
    const fecha = new Date(month + '-01-' + year).toString();
    return fecha;
  }

  imprimir() {
    window.print();
  }
  getInvoiceNumber(num: number): string {
    let displayed = num.toString();
    while (displayed.length <= 8) {
      displayed = '0' + displayed;
    }
    return displayed;
  }
  limpiar() {
    this.facturas = this.bufferFacturas;
    this.verNoPagadas = true;
    this.getIndex();
  }
  getIndex() {
    for (let i = 0; i < this.facturas.length; i++) {
      const factura = this.facturas[i];
      factura.index = i + 1;
    }
    this.getTotales();
  }
  initFacturas() {
    this._billService.getAll().subscribe(
      response => {
        console.log(response);
        if (response.Bill) {
          this.bufferFacturas = response.Bill;
          this.facturas = [];
          //  this.facturas = response.Bill;
          for (let i = 0; i < response.Bill.length; i++) {
            const factura = response.Bill[i];
            factura.index = i + 1;
            this.facturas.push(factura);
          }
          this.getTotales();
        }
      }
    );
  }
  ngOnInit() {

  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
}
