import { Component, OnInit, OnChanges } from '@angular/core';
import { RateService } from '../../../../../service/rate.service';
import { Rate } from '../../../../../models/rate';
import { ErrorCatcher } from '../../../../../models/errorCatcher';
import { ErrorCatcherService } from '../../../../../service/errorCatcher.service';
import { Limits } from '../../../../../models/limits';
import { LimitService } from '../../../../../service/limit.service';
import { Location } from '@angular/common';
import { User } from '../../../../../models/user';
import { ExtraService } from '../../../../../service/extra.service';


@Component({
  selector: 'app-show-rates',
  templateUrl: './showrates.component.html',
  styleUrls: ['./showrates.component.scss'],
  providers: [RateService, ErrorCatcherService, LimitService, ExtraService]
})
export class ShowRatesComponent implements OnInit, OnChanges {
  public identity: User;
  public title: string;
  public description: string;
  public url: string;
  public rates: Rate[] = [];
  public limits: Limits[] = [];
  public rateEdit;
  public loading: boolean;
  public page: number;
  public extra;
  /**
   * Registro de Errores Capturados @type ErrorCatcher
   */
  public errorCatched: ErrorCatcher;

  constructor(
    private location: Location,
    private rateService: RateService,
    private limitService: LimitService,
    /**
     * Servicio de Catpura de Errores
     */
    private errorCatcherService: ErrorCatcherService,
    private _extraService: ExtraService
  ) {
    window.document.title = 'MerAgua - Lista de Tarifas';
    this.title = 'Lista de Tarifas';
    this.description = 'Cargando....';
    this.loading = true;
    this.getData();
    this.identity = JSON.parse(localStorage.getItem('identity'));
    this.initExtras();
  }
  return() {
    this.location.back();
  }
  initExtras() {
    this._extraService.getExtra().subscribe(
      response => {
        response.extra.sort((menor, mayor) => {
          const a = new Date(menor.date);
          const b = new Date(mayor.date);
          if (a>b) {
            return -1
          }
          if (a<b) {
            return 1
          }
          if (a==b) {
            return 0
          }
        })
        this.extra = response.extra[0];
      }
    );
  }
  calcValorActual(costo: number): number {
    let valor
    costo = costo / 100;
    if (this.extra) {
      valor = costo * this.extra.rmuv;
    }
    return valor
  }
  ngOnChanges() {
    console.log('detectando cambios');

    this.getData();
    this.getLimits();
  }

  ngOnInit() {
    this.getData();
    this.getLimits();
  }
  editRate(rate) {
    this.rateEdit = rate;
  }
  getLimits() {
    this.limitService.getAllLimits().subscribe(
      response => {
        if (response.Limits.length >= 1) {
          this.description = 'Lista de tarifas Ingresadas en el sistema.';
          this.loading = false;
          this.limits = response.Limits;
          this.limits.sort((a, b) => (a.limit_from - b.limit_from));
          this.limits.sort((a, b) => {
            if (a.rate_id > b.rate_id) {
              return 1;
            }
            if (a.rate_id < b.rate_id) {
              return -1;
            }
            return 0;
          });
        } else {
          this.description = 'Aun no existen tarifas ingresadas.';
          this.loading = false;
        }
      }, error => {
        console.log(error as any);
      }
    );
  }
  getData() {
    this.rateService.getRate().subscribe(
      response => {
        this.rates = response.Rates;
      }, error => {
        console.log(error as any);
        this.errorCatched.description = error.toString() as any;
        this.errorCatched.table = 'Rate';
        this.errorCatched.action = 'showRate';
        this.errorCatched.title = 'Error al mostrar las Tarifas';
        this.errorCatched.zone = 'show-rate';
        this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
        this.errorCatcherService.saveError(this.errorCatched).subscribe(
          response => {
            alert(response.Message);
          });
      }
    );
    this.limitService.getAllLimits().subscribe(
      response => {
        if (response.Limits.length >= 1) {
          this.limits = response.Limits;
        } else {
          this.limits = null;
        }
      }, error => {
        console.log(error as any);
        this.errorCatched.description = error.toString() as any;
        this.errorCatched.table = 'Limits';
        this.errorCatched.action = 'showAllLimits';
        this.errorCatched.title = 'Error al mostrar los Limites';
        this.errorCatched.zone = 'show-rate';
        this.errorCatched.code = this.errorCatched.zone + '-' + this.errorCatched.action + '-' + this.errorCatched.table;
        this.errorCatcherService.saveError(this.errorCatched).subscribe(
          response => {
            alert(response.Message);
          });
      }
    );
  }
}
