import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { User } from '../../../../../models/user';
import { UserService } from '../../../../../service/user.service';
import { Global } from '../../../../../service/global';
import { FollowService } from '../../../../../service/follow.service';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';
@Component({
    selector: 'app-users',
    templateUrl: './users.component.html',
    styleUrls: ['./users.component.scss'],
    providers: [UserService, FollowService]
})

export class UsersComponent implements OnInit, OnChanges {

  public identity: User;
  public token: any;
  public url: string;
  public nextPage: number;
  public prevPage: number;
  public status: string;
  public total: any;
  public pages: number;
  public page: number;
  public usuarios: User[] = [];
  public description: string;
  public followMouseOver: number;
  public role_user: string;
  @Input() employees: boolean;
  @Input() SearchedUsers: string;
  @Output() EnvioDeUsuario = new EventEmitter();
  @Output() EnvioDeUsuarios = new EventEmitter();

  constructor(
    private userService: UserService,
    private router: Router
  ) {
    const user = JSON.parse(localStorage.getItem('identity'));
    this.role_user = user.role_user;

    this.url = Global.url + 'user/';
    this.description = 'Mostrando la lista actual de usuarios registrados en el sistema';
    this.identity = user;
    this.token = this.userService.getToken();
  }

  showUser(id) {
    localStorage.setItem('user', id);
    this.router.navigate(['/perfil-usuario']);
    // window.open('/perfil-usuario', 'blank')
  }
  ngOnInit() {
    this.getUsers();
    if (this.employees) {
      console.log('vista de empleados');
    }
  }
  getRoleUser(role: string): string {
    let displayed: string;
    role = role.toLowerCase();
    if (role === 'common' || role === 'simple') { displayed = 'Consumidor'; } else
    if (role === 'administrador' || role === 'admin') { displayed = 'Administrador'; } else
    if (role === 'operador') { displayed = 'Operador'; } else
    if (role === 'secretario') { displayed = 'Secretario'; } else { displayed = role; }
    return displayed;
  }
  ngOnChanges() {
    this.getUsers();
    if (this.employees) {
      console.log('vista de empleados');
    }
  }
  // ------------------------------------------------------------------- //
  // Funcion de obtener usuarios General
  // ------------------------------------------------------------------- //
  getUsers() {

    if (!this.SearchedUsers) {
      // llamado al servicio con el metodo ShowRaw que devuelve los usuarios sin paginar
      this.userService.showRaw().subscribe(
        // respuesta del servidor //
        response => {
          if (this.employees) {
            this.usuarios = [ ] , [ ];
            // tslint:disable-next-line: prefer-for-of
            for (let index = 0; index < response.Usuarios.length; index++) {
              const usuario = response.Usuarios[index];
              if (usuario.role_user.toLowerCase() !== 'simple' && usuario.role_user.toLowerCase() !== 'common') {
                this.usuarios.push(usuario);
              }
            }
          } else if (!this.employees) {
            const users = JSON.stringify(response.Usuarios);
            this.usuarios = response.Usuarios;
            this.EnvioDeUsuarios.emit(users);
          }
          // asignacion a la variable local "usuarios" de la coleccion de "Usuarios" almacenada en la respuesta [response]

        },
        // error del servidor //
        error => {
            // Detalla el error en la consola... //
            console.log(error as any);
        }
      );
    } else {
      this.usuarios = JSON.parse(this.SearchedUsers);
    }

  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
  editarUser(user) {
    if (user.role_user.toLowerCase() == 'common' || user.role_user.toLowerCase() == 'simple') {
      user = JSON.stringify(user);
      this.EnvioDeUsuario.emit(user);
    } else {
      this.launchAlert(
        'error',
        'Sin Permisos',
        'No se Puede editar Empleados!',
        null,
        false,
        3500,
        false,
        'top-end',
        'row',
        true
      );
    }
  }

  // ********************************************************************************************************* //

}
