import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Importe } from '../../../../models/importe';
import { ImporteService } from '../../../../service/importe.service';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';


@Component({
    selector: 'app-importes',
    templateUrl: './importes.component.html',
    styleUrls: ['./importes.component.scss'],
    providers: [ImporteService]
})

export class ImportesComponent implements OnInit {
    public title: string;
    public loading: boolean;
    public description: string;
    public importe: Importe;
    public identity;
    public importes: Importe [] = [];
    public editing: boolean = false;
    public reload: boolean;
    constructor(
        private location: Location,
        private importeService: ImporteService,
        private router: Router
    ) {
    window.document.title = 'MerAgua - Lista de Importes y Descuentos';
    this.title = 'Cargando...';
        this.identity = JSON.parse(localStorage.getItem('identity'));
        if (this.identity.role_user.toLowerCase() == 'operador') this.router.navigate(['/inicio']);
        this.description = 'Cargando...';
        this.loading = true;
        this.initImporte();
        this.editing = false;
        this.reload = false;
    }
    onDescuento() {
        if (this.importe.descuento && this.importe.descuento.toString() == 'true') {
            this.importe.percent = true;
        }
    }
    initImporte() {
        this.importe = {
            _id: null,
            nombre: null,
            costo: null,
            percent: null,
            descuento: null,
            edited_by: null,
            edited_at: null
        }
    }
    ngOnInit() {
        this.title = 'Pgina de Importes Y Descuentos';
        this.description = 'Ventana de Ingreso y Edicin de los Importes y Descuentos aplicables a Facturas'
        this.loading = false;
    }
    return() {
      this.location.back();
    }

    save() {
        if (this.editing === false) {
            this.importeService.save(this.importe).subscribe(
                response => {
                    alert(response.Message);
                    this.initImporte();
                }, error => {
                    console.log(error as any);
                }
            )
        } else if (this.editing === true) {
            this.importeService.updateImporte(this.importe).subscribe(
                response => {
                    alert(response.Message);
                }, error => {
                    console.log(error as any);
                }
            );
        }
        this.reload = true;
        setTimeout(() => {
            this.reload = false;
        }, 250);
    }
    onSubmit() {
        if (this.importe.percent.toString() == 'true' && this.importe.descuento.toString() == 'true' && this.importe.costo > 75) {
            this.launchAlert('error', 'El descuento porcentual no debe sobrepasar el 75%', null, null, null, 3500, false, 'top', 'row', true);
        } else {
            this.save();
        }
    }
    onEdit(event){
        this.editing = true;
        const importe = JSON.parse(event);
        this.importe = importe;
    }
    onClear() {
        this.editing = false;
        this.initImporte();
    }
    launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
        Swal.fire({
          icon,
          title,
          text,
          footer,
          showConfirmButton,
          timer,
          backdrop,
          position,
          grow,
          toast
        });
      }
}
