import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';

@Injectable()
export class NumerosService {
    public url: string;
    public identity;
    public token;
    constructor(
        private _http: HttpClient
    ) {
        this.url = Global.url + 'numero/';
    }
    // -----Devolver Usuario-----------------------------------------------------//
    getIdentity() {
        const identity = JSON.parse(localStorage.getItem('identity'));
        if (identity != 'undefined') {
            this.identity = identity;
        } else if (identity == 'undefined') {
            this.identity = null;
        }
        return this.identity;
    }
    // -----Devolver Token-------------------------------------------------------//
    getToken() {
        const token = JSON.parse(localStorage.getItem('token'));
        if (token != 'undefined') {
            this.token = token;
        } else if (token == 'undefined') {
            this.token = null;
        }
        return this.token;
    }
    showRaw(): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url + 'listar', { headers });
    }
    save(facturaNumero): Observable<any> {
        const params = JSON.stringify(facturaNumero);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
            .set('Authorization', this.getToken());
        return this._http.post(this.url + 'guardar', params, { headers });
    }
    update(facturaNumero): Observable<any> {
        const params = JSON.stringify(facturaNumero);
        const headers = new HttpHeaders().set('Content-Type', 'application/json')
          .set('Authorization', this.getToken());
        return this._http.put(this.url + 'editar/' + facturaNumero._id, params, { headers });
      }
}
