import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { SectorService } from '../../../../../service/sector.service';
import { Sector } from '../../../../../models/sector';
import Swal from 'sweetalert2';
import { StatisticsService } from '../../../../../service/statistics.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-list-sector',
  templateUrl: './list-sector.component.html',
  styleUrls: ['./list-sector.component.css'],
  providers: [SectorService, StatisticsService]
})
export class ListSectorComponent {
  public title: string;
  public sectores: Sector[];
  public idSelected: string;
  @Output() selectSector = new EventEmitter();
  constructor(
    private _sectorService: SectorService,
    private _statisticService: StatisticsService,
    private _location: Location
  ) {
    this.title = 'Lista de Sectores';
    this.initSectores();
    this.sectores = [];
   }
  onEdit(sector) {
    sector = JSON.stringify(sector);
    this.selectSector.emit(sector);
  }
  onChange(sector) {
    const tempSector = sector;
    let estado: string;
    if (sector.activa) {
      sector.activa = false;
    } else if (!sector.activa) {
      sector.activa = true;
    }
    this._sectorService.updateSectores(sector).subscribe(
      response => {
        sector = response.sector;
        if (sector.activa) estado = 'Reactivar';
        if (!sector.activa) estado = 'Desactivar';
        this.launchAlert(
          'success',
          'Cambio Estado',
          'Exito al ' + estado + ' el sector',
          null,
          null,
          3500,
          false,
          'top',
          'row',
          true
        );
      }, error => {
        if (sector.activa) estado = 'Reactivar';
        if (!sector.activa) estado = 'Desactivar';
        sector = tempSector;
        this.launchAlert(
          'error',
          'Error al ' + estado + ' el sector',
          error as any,
          null,
          null,
          3500,
          false,
          'top',
          'row',
          true
        );
      }
    );
  }
  onHover(id) {
    this.idSelected = id;
  }
  onLeave() {
    this.idSelected = null;
  }
  initSectores() {
    this._sectorService.getSectores().subscribe(
      response => {
        console.log(response);
        
        for (let sector of response.sectores) {
          this._statisticService.getMedidoresSector(sector._id).subscribe(
            responseCounters => {
              sector.meters = responseCounters.meters;
            }
          );
        }
        this.sectores = response.sectores;
      }, error => {
        console.error(error as any);
      }
    );
  }
  return() {
    this._location.back();
  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
}
