import { Component, OnInit } from '@angular/core';
import { MeterService } from '../service/meter.service';
import { RegisterService } from '../service/register.service';
import { User } from '../models/user';
import { Meter } from '../models/meter';
import { UserService } from '../service/user.service';
import { Router } from '@angular/router';
import * as moment from 'moment';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-consultas',
    templateUrl: './consultas.component.html',
    styleUrls: ['./consultas.component.scss'],
    providers: [MeterService, RegisterService, UserService]
})
export class ConsultasComponent implements OnInit {
    public userList: any[];
    public searchTitle: string;
    public searchingBy: string;
    public searchPlaceholder: string;
    public tipo: string = 'text';
    public usuarioActual;
    public meterList: any[];
    public medidorActual;
    public registerList;
    public meter: Meter;
    public meterId;
    public loading: boolean;
    public meters;
    public desde;
    public validDate: boolean;
    public hasta;
    public bufferRegisters;
    /** Conjunto de Usuarios @type User [ ] = [ ] */
    public usuarios: User[] = [];
    public identity;
    constructor(
        private meterService: MeterService,
        private userService: UserService,
        private router: Router,
        private registerService: RegisterService
    ) {
        this.loading = true;
        this.validDate = false;
        this.searchTitle = 'Seleccionar Termino de Busqueda';
        this.searchPlaceholder = this.searchTitle;
        this.initUsuarios();
        this.initMeters();
    }

    ngOnInit() {
    }
    launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
        Swal.fire({
            icon,
            title,
            text,
            footer,
            showConfirmButton,
            timer,
            backdrop,
            position,
            grow,
            toast
        });
    }
    validateDate() {
        if (this.desde && this.hasta) {
            const fechaDesde = moment(this.desde);
            const fechaHasta = moment(this.hasta);
            const difference = fechaHasta.diff(fechaDesde, 'days');
            if (difference < 0) {
                this.validDate = false;
                this.launchAlert(
                    'error',
                    'Error, la Fecha de inicio no puede superar a la Fecha de fin',
                    null,
                    null,
                    null,
                    3500,
                    false,
                    'top',
                    'row',
                    true
                );
            } else {
                this.validDate = true;
                this.filterByDate();
            }
        }
    }
    filterByDate() {
        this.registerList = [];
        for (const registro of this.bufferRegisters) {
            const fechaDesde = moment(this.desde);
            const fechaHasta = moment(this.hasta);
            let fechaFactura;
            let diffMin;
            let diffMax;
            fechaFactura = moment((registro.month + 1) + '-01-' + registro.year);
            diffMin = fechaFactura.diff(fechaDesde, 'months');
            diffMax = fechaFactura.diff(fechaHasta, 'months');
            if (diffMin >= 0 && diffMax <= 0) {
                this.registerList.push(registro);
            }
        }
    }
    limpiarFiltro() {
        this.registerList = this.bufferRegisters;
        this.desde = null;
        this.hasta = null;
    }
    changeSearch(search) {
        this.userList = null;
        this.loading = true;
        this.meterList = null;
        this.registerList = null;
        this.searchingBy = search;
        if (search == 'nombres') {
            this.tipo = 'text';
            this.searchTitle = 'Buscando por Nombres';
            this.searchPlaceholder = 'Ingresar Nombre a Buscar';
        }
        if (search == 'cedula') {
            this.tipo = 'number';
            this.searchPlaceholder = 'Ingresar Cdula a Buscar';
            this.searchTitle = 'Buscando por Cdula';
        }
        if (search == 'medidor') {
            this.tipo = 'number';
            this.searchPlaceholder = 'Ingresar Clave del Medidor a Buscar';
            this.searchTitle = 'Buscando por Medidor';
        }
    }
    searchUser(searchWord: string) {
        if (searchWord.length >= 1) {
            switch (this.searchingBy) {
                case 'nombres':
                    this.registerList = null;
                    this.userList = [];
                    searchWord = searchWord.toLowerCase().replace(/ /gi, '');
                    for (let i = 0; i < this.usuarios.length; i++) {
                        const usuario = this.usuarios[i];
                        let user = usuario.nombre.toLowerCase().replace(/ /gi, '');
                        user = user + usuario.apellido.toLowerCase().replace(/ /gi, '');
                        if (user.indexOf(searchWord, 0) >= 0) {
                            this.userList.push(usuario);
                        }
                    }
                    break;
                case 'cedula':
                    this.registerList = null;
                    this.userList = [];
                    for (let i = 0; i < this.usuarios.length; i++) {
                        const usuario = this.usuarios[i];
                        let user = usuario.cedula.replace('-', '');
                        if (user.indexOf(searchWord, 0) >= 0) {
                            console.log('usuario encontrado por cedula');
                            this.userList.push(usuario);
                        }
                    }
                    break;
                case 'medidor':
                    this.registerList = null;
                    this.meterList = [];
                    for (let i = 0; i < this.meters.length; i++) {
                        const medidor = this.meters[i];
                        const clave = medidor.clave.toString();
                        if (clave.indexOf(searchWord, 0) >= 0) {
                            this.meterList.push(medidor);
                        }
                    }
                    break;
            }
        } else {
            this.userList = undefined;
        }
        if (this.userList && this.userList.length == 1) {
            this.usuarioActual = this.userList[0]._id;
            this.searchMeters();
        }
    }
    searchMeters() {
        this.meterList = [];
        for (const medidor of this.meters) {
            if (medidor.user._id == this.usuarioActual) {
                this.meterList.push(medidor);
            }
        }
        if (this.meterList.length == 1) {
            this.medidorActual = this.meterList[0]._id;
            this.searchRegisters();
        }
    }

    searchRegisters() {
        this.registerList = null;
        this.registerList = [];
        if (!this.userList || this.userList.length <= 0) {
            this.meterService.getMeterForConsulting(this.medidorActual).subscribe(
                response => {
                    this.userList = [];
                    this.userList.push(response.meter.user);
                }
            );
        }
        this.registerService.getByMeterForConsulting(this.medidorActual).subscribe(
            response => {
                console.log(response);
                if (response.Registros && response.Registros.length >= 1) {
                    for (let i = 0; i < response.Registros.length; i++) {
                        const registro = response.Registros[i];
                        registro.numero = i + 1;
                        this.registerList.push(registro);
                    }
                    this.registerList.sort((a,b) => {
                        const fecha1 = moment((a.month + 1) + '-01-' + a.year);
                        const fecha2 = moment((b.month + 1) + '-01-' + b.year);
                        const difference = fecha1.diff(fecha2, 'months');
                        if (difference >=1 ) return -1;
                        if (difference <=-1 ) return 1;
                        if (difference == 0 ) return 0;
                    });
                    this.bufferRegisters = this.registerList;
                } else {
                    this.registerList = null;
                }
            }
        );
    }

    getDate(month, year): string {
        month++;
        const fecha = new Date(month + '-01-' + year).toString();
        return fecha;
    }

    initMeters() {
        this.loading = false;
        this.meterService.showMetersForConsulting().subscribe(
            response => {
                this.meters = response.Medidores;
            }, error => {
                console.log(error as any);
            }
        );
    }


    selectRegister(id, cancelado, facturado) {
        if (!cancelado && !facturado) {
            localStorage.setItem('register', id);
            this.router.navigate(['/electronica']);
        } else if (!cancelado && facturado) {
            localStorage.setItem('invoice', id);
            this.router.navigate(['/electronica']);
        } else if (cancelado && facturado) {
            localStorage.setItem('invoice', id);
            this.router.navigate(['/electronica']);
        }
    }

    initUsuarios() {
        this.userService.showForConsulting().subscribe(
            response => {
                this.usuarios = response.Usuarios;
            }, error => {
                console.log(error as any);
            });
    }
}
