import { Injectable } from '@angular/core';
import { Global } from './global';
import { Observable } from 'rxjs';
import { HttpHeaders, HttpClient } from '@angular/common/http';


@Injectable()
export class StatisticsService {
    public url: string;
    constructor(
        private http: HttpClient
    ) {
        this.url = Global.url + 'statistics/';
    }

    getUserStatistics(id): Observable<any> {
        const headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this.http.get(this.url + 'get-statistics-user/' + id, { headers });
    }

    getBillsNumbers(): Observable<any> {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.get(this.url + 'get-bills-numbers', { headers });
    }
    getCedulas(): Observable<any> {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.get(this.url + 'get-cedulas', { headers });
    }
    getSectores(): Observable<any> {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.get(this.url + 'get-sectores', { headers });
    }
    getMedidoresSector(sector): Observable<any> {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.get(this.url + 'get-medidores-sector/' + sector, { headers });
    }
    getGlobals(): Observable<any> {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.get(this.url + 'get-statistics-global', { headers });
    }
    getMetersNumbers(): Observable<any> {
      const headers = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.get(this.url + 'get-meters-numbers', { headers });
    }
    getGlobalStatistics(): Observable<any> {
      const token = JSON.parse(localStorage.getItem('token'));
      const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                       .set('Authorization', token);
      return this.http.get(this.url + 'get-globals', { headers });
    }
}
