import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { StatisticsService } from '../../../../service/statistics.service';
import { Location } from '@angular/common';
import { BillService } from '../../../../service/bill.service';
import { ImporteService } from '../../../../service/importe.service';
import { DetalleFacturaService } from '../../../../service/detalleFactura.service';

@Component({
  selector: 'app-records',
  templateUrl: './records.component.html',
  styleUrls: ['./records.component.scss'],
  providers: [StatisticsService, BillService, ImporteService, DetalleFacturaService]
})
export class RecordsComponent implements OnInit {
  public identity;
  public statistics;
  public rateStatistics;
  public globals;
  public facturaChart;
  constructor(
    private _router: Router,
    private _statisticService: StatisticsService,
    private _location: Location,
    private _billService: BillService,
    private _importService: ImporteService,
    private _detalleService: DetalleFacturaService
  ) {
    window.document.title = 'MerAgua - Reportes';
    this.identity = JSON.parse(localStorage.getItem('identity'));
    if (this.identity === null || this.identity === undefined || this.identity === 'null' || this.identity === 'undefined') {
      this._router.navigate(['/login']);
    } else if (this.identity.role_user.toLowerCase() != ('admin' || 'administrador')) {
      this._router.navigate(['/inicio']);
    }
    this.facturaChart = {
      barChartData: [],
      barChartLabels: [],
      barChartOptions: {
        responsive: true
      },
      barChartLegend: true,
      barChartType: null
    };
    this.initDetalles();
    this.initBills();
    
  }
  public detalles;
  initDetalles() {
    this._detalleService.getDetalles().subscribe(
      response => {
        this.detalles = response.Detalles;
        this.initImportes();
      }
    )
  }
  public facturas;
  initBills() {
    this._billService.getAll().subscribe(
      response => {
        this.facturas = response.Bill;
        this.initFacturaChart();
        this.initStatistics();
      }
    );
  }
  public importes;
  initImportes() {
    this._importService.getImportes().subscribe(
      response => {
        this.importes = response.Importes;
        this.getCostByImports();
      }
    )
  }
  initFacturaChart() {
    let facturas = [{
      tipo: 'debe',
      facturas: []
    }];
    facturas.push({ tipo: 'efectivo', facturas: [] });
    facturas.push({ tipo: 'tarjeta', facturas: [] });
    facturas.push({ tipo: 'transferencia', facturas: [] });
    let counters = [];
    for (const pago of facturas) {
      let count = 0;
      for (const factura of this.facturas) {
        if (factura.pago.toLowerCase() == pago.tipo.toLowerCase()) {
          pago.facturas.push(factura);
          count++;
        }
      }
      pago.tipo = pago.tipo.toLocaleUpperCase() + ': ' + count;
      counters.push(count);
      this.facturaChart.barChartLabels.push(pago.tipo);
    }
    this.facturaChart.barChartData.push({ data: counters, label: 'Facturas' });
    // this.facturaChart.barChartData = [
    //   { data: [65, 23, 422, 123, 4, 445], label: 'Series A' },
    //   { data: [555, 223, 42, 143, 55, 45], label: 'Series B' },
    //   { data: [125, 255, 44, 244, 3, 25], label: 'Series C' }
    // ];
    this.facturaChart.barChartType = 'polarArea';

  }
  verChartFactura() {
    if (this.facturaChart.barChartType == 'polarArea') {
      this.facturaChart.barChartType = 'radar';
    } else if (this.facturaChart.barChartType == 'radar') {
      this.facturaChart.barChartType = 'polarArea';
    }
  }
  getCostByImports() {
    let costoTotal = 0;
    let costoConsumo;
    for (const importe of this.importes) {
      costoConsumo = 0;
      for (const detalle of this.detalles) {
        if (detalle.importe && detalle.importe._id == importe._id) {
          costoConsumo += detalle.subtotal;
          costoTotal += detalle.subtotal;
        }
      }
      importe.costoConsumo = costoConsumo;
    }
    this.importes.costoTotal = costoTotal;

  }
  initStatistics() {
    this._statisticService.getGlobals().subscribe(
      response => {
        this.statistics = response.statistics;
      }
    );
    this._statisticService.getGlobalStatistics().subscribe(
      responseGlobal => {
        let tarifas;
        this.globals = responseGlobal.globals;
        tarifas = this.globals.tarifas;
        tarifas.medidores = [];
        let medidores = [];
        for (const medidor of this.globals.medidores) {
          medidor.registros = []
          medidor.consumo = 0;
          let count = 0;
          for (const registro of this.globals.registros) {
            if (medidor._id == registro.meter) {
              medidor.registros.push(registro);
              medidor.consumo = medidor.consumo + registro.consumo;
              count++;
            }
          }
          if (count >= 1) medidor.consumo = medidor.consumo / count;
        }
        let consumoTotal = 0;
        let costoTotal = 0;
        for (const tarifa of tarifas) {
          tarifa.consumo = 0;
          tarifa.medidores = 0;
          tarifa.costo = 0;
          for (const medidor of this.globals.medidores) {
            if (medidor.rate == tarifa._id) {
              tarifa.medidores++;
              tarifa.consumo = tarifa.consumo + medidor.consumo;
              for (const factura of this.facturas) {
                if (factura.medidor.id == medidor._id) {
                  tarifa.costo += factura.total;
                  costoTotal += factura.total;
                }
              }
            }
          }
          consumoTotal = consumoTotal + tarifa.consumo;
        }
        this.rateStatistics = tarifas;
        // calcular el costo de ingreso promedio
        this.rateStatistics.consumoTotal = consumoTotal;
        this.rateStatistics.costoTotal = costoTotal;
        this.rateStatistics = this.rateStatistics.sort((a, b) => (a.consumoTotal - b.consumoTotal));
      }, error => {
        console.error(error as any);
      }
    );
  }
  calcularPorcentaje(parte: number, total: number): number {
    let porcentaje = (parte * 100) / total;
    porcentaje = parseFloat(porcentaje.toFixed(2));
    return porcentaje;
  }
  return() {
    this._location.back();
  }
  ngOnInit() {
  }

}
