import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './logins/components/login/login.component';
import { NotFoundComponent } from './client/components/notfound/notfound.component';
import { ImprimirFacturaComponent } from './ImprimirFactura/ImprimirFactura.component';
import { ImprimirElectronicaComponent } from './imprimir-electronica/imprimir-electronica.component';
import { ConsultasComponent } from './consultas/consultas.component';

// Componentes
const routes: Routes = [
  { path: 'login', component: LoginComponent, data: {titulo: 'Iniciar sesin'} },
  { path: 'not-found', component: NotFoundComponent },
  { path: 'consultas', component: ConsultasComponent },
  // { path: 'factura', component: ImprimirFacturaComponent },
  // { path: 'factura-electronica', component: ImprimirElectronicaComponent },
  { path: '**',  redirectTo: 'not-found'},
];

export const appRoutingProviders: any[] = [];
export const routing: ModuleWithProviders = RouterModule.forRoot(routes);
