import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Global } from './global';
import { Observable } from 'rxjs';
import { Sector } from '../models/sector';

@Injectable()
export class SectorService {
  public url: string;

  constructor(
    private http: HttpClient
  ) {
    this.url = Global.url + 'sector/';
  }

  // Metodos------------------------------------------------------------------//
  // ------Get----------------------------------------------------------------//
  getSectores(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                      .set('Authorization', token);

    return this.http.get(this.url + 'shows', { headers });
  }
  getSector(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                      .set('Authorization', token);
    console.log(this.url, 'show/', id);
    return this.http.get(this.url + 'show/' + id, { headers });
  }
  // ------Post---------------------------------------------------------------//
  saveSectores(sector: Sector): Observable<any> {
    const params = JSON.stringify(sector);
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                      .set('Authorization', token);

    return this.http.post(this.url + 'save', params, { headers });
  }
  // ------Post---------------------------------------------------------------//
  updateSectores(sector: Sector): Observable<any> {
    const params = JSON.stringify(sector);
    const token = JSON.parse(localStorage.getItem('token'));
    const headers = new HttpHeaders().set('Content-Type', 'application/json')
                                      .set('Authorization', token);

    return this.http.put(this.url + 'update/' + sector._id, params, { headers });
  }
}
