// tslint:disable: variable-name
import { Component, OnInit } from '@angular/core';
import { NumerosService } from '../../../../../service/numeros.service';
import { Numeros } from '../../../../../models/numeros';

@Component({
  selector: 'app-listar-numeros',
  templateUrl: './listar-numeros.component.html',
  styleUrls: ['./listar-numeros.component.css'],
  providers: [NumerosService]
})
export class ListarNumerosComponent implements OnInit {
  public title: string;
  public cargando: boolean;
  public numeros: Numeros[];
  constructor(
    private _numerosService: NumerosService
  ) {
    this.title = 'Cargando';
    this.cargando = true;
    this.initNumeros();
   }
   initNumeros() {
    this._numerosService.showRaw().subscribe(
      response => {
        console.log(response);
        this.numeros = response.Numeros;
        this.cargando = false;
        this.title = 'Rangos en el Sistema';
      }
    );
   }

  ngOnInit() {
  }

}
