import { Component, OnInit } from '@angular/core';
import { MeterService } from '../../../../../service/meter.service';
import { Meter } from '../../../../../models/meter';
import { Global } from '../../../../../service/global';
import { UploadService } from '../../../../../service/upload.service';
import { UserService } from '../../../../../service/user.service';
import { ActivatedRoute } from '@angular/router';
import { RateService } from '../../../../../service/rate.service';
import { User } from '../../../../../models/user';
import { Sector } from '../../../../../models/sector';
import { SectorService } from '../../../../../service/sector.service';
import Swal from 'sweetalert2';
import { StatisticsService } from '../../../../../service/statistics.service';


@Component({
  selector: 'app-edit-medidor',
  templateUrl: './editmedidor.component.html',
  styleUrls: ['./editmedidor.component.scss'],
  providers: [MeterService, UserService, UploadService, RateService, SectorService, StatisticsService]
})

export class EditMedidorComponent implements OnInit {
  public title: string;
  public medidor: Meter;
  public url: string;
  public error: boolean;
  public token;
  public usuarios;
  public user: User;
  public users: User[] = [];
  public tarifas;
  public conteos;
  public image: string;
  public filesToUpload: Array<File>;
  public sectores: Sector[];
  public medidorOk: boolean;
  public claves;
  constructor(
    private meterService: MeterService,
    private uploadService: UploadService,
    private userService: UserService,
    private rateService: RateService,
    private route: ActivatedRoute,
    private _sectorService: SectorService,
    private _statisticsService: StatisticsService
  ) {
    this.title = 'Edicion de Medidores...';
    this.url = Global.url + 'meter/';
    window.document.title = 'MerAgua - Editando Datos del Medidor';
    this.token = this.userService.getToken();
    this.user = new User(
      '',    // "_id"
      null,    // "cuenta"
      null,    // "contrase"
      null,    // "correo"
      '',    // "nombre"
      '',    // "image"
      '',    // "apellido"
      '',    // "cedula"
      '',    // "direccion"
      '',    // "telefono"
      '',    // "fecha_nacimiento"
      '',    // "sexo"
      'Simple',    // "role_user"
      true,    // "activa"
      ''
    );
    this.route.params.subscribe(params => {
      const id = params.id;
      this.meterService.getMeterSimple(id).subscribe(
        response => {
          console.log('medidor Obtenido!');
          this.medidor = response.meter;
          window.document.title = 'MerAgua - Editando Datos del Medidor' + this.medidor.clave;

        }, error => {
          console.log(error as any);
        }
      );
    });
    this.InitSectores();
    this.InitClaves();
  }
  InitClaves() {
    this._statisticsService.getMetersNumbers().subscribe(
      response => {
        console.log(response);
        if (response.Claves) this.claves = response.Claves;
        if (!response.Claves) this.launchAlert('alert', response.Message, null, null, false, 1500, false, 'top', 'row', true);
      }
    );
  }
  validarMedidor(clave) {
    clave = JSON.stringify(clave);
    console.log(clave.length);
    if (clave !== undefined) {
      if (clave.length <= 5 || clave.length >= 11) {
        this.medidorOk = null;
        let message: string;
        if (clave.length <= 5) message = 'Es necesario Ingresar al menos 6 dgitos.';
        if (clave.length >= 10) message = 'la clave no puede exceder los 10 dgitos.';
        this.launchAlert('error', 'Clave Incorrecta, ', message, null, false, 1500, false, 'top', 'row', true);
      } else if (clave.length >= 6 && clave.length <= 10) {
        let count = 0;

        for (const medidor of this.claves) {
          if (JSON.stringify(medidor.clave) == clave) {
            this.launchAlert('info', 'Comprobando Clave: ',
            clave,
            null, false, 1500, false, 'top', 'row', true);
            count++;
          }
        }
        if (count >= 1) {
          this.medidorOk = false;
          this.launchAlert('error', 'Clave Incorrecta, ',
          'Clave de medidor repetida: Ya existe un medidor con esa clave!',
          null, false, 1500, false, 'top', 'row', true);
        } else if (count === 0) {
          this.launchAlert('success', 'Clave correcta!, ', null,
          null, false, 1500, false, 'top', 'row', true);
          this.medidorOk = true;
        }
      }
    }
  }
  launchAlert(icon, title, text, footer, showConfirmButton, timer, backdrop, position, grow, toast) {
    Swal.fire({
      icon,
      title,
      text,
      footer,
      showConfirmButton,
      timer,
      backdrop,
      position,
      grow,
      toast
    });
  }
  InitSectores() {
    this._sectorService.getSectores().subscribe(
      response => {
        this.sectores = [];
        for (const sector of response.sectores) {
          if (sector.activa) {
            this.sectores.push(sector);
          }
        }
      }, error => {
        console.error(error as any);
      }
    );
  }
  ngOnInit() {
    this.cargarDatos();
  }
  onSubmit() {
    this.meterService.updateMeter(this.medidor).subscribe(
      response => {
        if (!response.meter) {
          this.error = true;
          alert(response.Message);
        } else {
          this.error = null;
          this.uploadService.makeFileRequest(
            this.url + 'image/' + this.medidor._id,
            [],
            this.filesToUpload,
            this.token, 'image')
            .then((result: any) => {
              console.log('subiendo la imagen');
              console.log(result);
            }).catch((error: any) => {
              console.log(error);
            });
          alert(response.Message);
        }
      }
    );
  }
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = fileInput.target.files as Array<File>;
    this.image = this.filesToUpload[0].name;
    console.log(this.image);

    console.log(this.filesToUpload);
  }

  cargarDatos() {
    console.log('Cargando datos');
    this.getUsers();
    this.getRates();
  }
  getUsers() {
    console.log('Cargando Datos de Usuarios');

    this.userService.showRaw().subscribe(
      response => {
        this.usuarios = response.Usuarios;
        // if (this.users.length <=0) {
        this.users = response.Usuarios;
        // }
      }, error => {
        console.log(error as any);
      }
    );
  }
  getRates() {
    console.log('Cargando datos de Tarifas');

    this.rateService.getRate().subscribe(
      response => {
        this.tarifas = response.Rates;
      }, error => {
        console.log(error as any);
      }
    );
  }
  busquedaUsuario(termino, porNombre, porCedula) {
    let busqueda;
    termino = termino.toLowerCase();
    let conteo = +0;
    this.conteos = null;
    this.users = [], [];
    this.usuarios.forEach(item => {
      if (porNombre == true && porCedula == true) {
        busqueda = item.nombre.replace(/ /g, '').toLowerCase() + '' + item.apellido.replace(/ /g, '').toLowerCase() + item.cedula.replace('-', '');
      } else if (porNombre == true && porCedula == false) {
        busqueda = item.nombre.replace(/ /g, '').toLowerCase() + '' + item.apellido.replace(/ /g, '').toLowerCase();
      } else if (porNombre == false && porCedula == true) {
        busqueda = item.cedula.replace('-', '');
      }
      if (porNombre == false && porCedula == false) {
        this.getUsers();
      } else {
        if (busqueda.indexOf(termino, 0) >= 0) {
          this.users.push(item);
          conteo = conteo + 1;
        }
      }
    });
    if (this.users.length <= 0) {
      this.getUsers();
      conteo = 0;
      console.log(conteo);
    }
    this.conteos = conteo;
  }

}
